<template>
  <div id="bc">
    <!-- 头部蓝条 -->
    <div id="fourth_top">
      <img @click.stop="clickBack()" id="backimg" src="../imgs/back.png" alt>
      <span>我的</span>
      <!-- 中间登录部分 -->
      <div id="top_login" @click="goLogin()">
        <div>
          <img id="img1" :src="imgU" alt>
        </div>
        <div>
          <p>{{useMsg||oldname}}</p>
          <p class="phone">
            <img id="img2" src="../imgs/shouji.png" alt>暂无绑定手机号
          </p>
        </div>
        <div>
          <img id="img3" src="../imgs/iconfontjiantou2.png" alt>
        </div>
      </div>
    </div>
    <!-- 中间余额 -->
    <div>
      <ul id="fourth_center">
        <li class="lis" @click="goyue()">
          <p>
            <span id="span1">0.00</span>
            <span>元</span>
          </p>
          <p>我的余额</p>
        </li>
        <li class="lis" @click="goyouhui()">
          <p>
            <span id="span2">{{hongbaoC||0}}</span>
            <span>个</span>
          </p>
          <p>我的优惠</p>
        </li>
        <li class="lis" @click="goMyJifen()">
          <p>
            <span id="span3">0</span>
            <span>分</span>
          </p>
          <p>我的积分</p>
        </li>
      </ul>
    </div>
    <!-- 订单积分部分 -->
    <div>
      <ul id="fourth_thid">
        <li @click="goThird()">
          <img src="../imgs/dingdan.png" alt>
          <span>我的订单</span>
          <img src="../imgs/right.png" alt>
        </li>
        <li @click="goJifen()">
          <img src="../imgs/tese.png" alt>
          <span>积分商城</span>
          <img src="../imgs/right.png" alt>
        </li>
        <li @click="goVip()">
          <img src="../imgs/huangguan.png" alt>
          <span>饿了么会员卡</span>
          <img src="../imgs/right.png" alt>
        </li>
      </ul>
    </div>
    <div>
      <ul id="fourth_four">
        <li @click="goFwCenter()">
          <img src="../imgs/bao.png" alt>
          <span>服务中心</span>
          <img src="../imgs/right.png" alt>
        </li>
        <li @click="goDownloadEleme()">
          <img src="../imgs/eliaomo.png" alt>
          <span>下载饿了么APP</span>
          <img src="../imgs/right.png" alt>
        </li>
      </ul>
    </div>
    <!-- 底部固定部分 -->
    <div id="change_botton">
      <router-link to="/first">
        <div>
          <img src="../imgs/SSS.png" alt>
          <p>外卖</p>
        </div>
      </router-link>
      <router-link to="/second">
        <div>
          <img src="../imgs/zhinanzhen.png" alt>
          <p>搜索</p>
        </div>
      </router-link>
      <router-link to="/third">
        <div>
          <img src="../imgs/icon-.png" alt>
          <p>订单</p>
        </div>
      </router-link>
      <router-link to="/fourth">
        <div>
          <img src="../imgs/touxiang1.png" alt>
          <p>我的</p>
        </div>
      </router-link>
    </div>
  </div>
</template>
<script>
import { Loading } from "element-ui";
let loadingInstance;
export default {
  name: "fourth",
  data() {
    return {
      oldname: "登录/注册",
      useMsg: this.$store.state.userMsg.username,
      imgU: "",
      hongbaoC: ""
    };
  },
  created() {
    if (this.imgU == "") {
      this.imgU = require("../imgs/fourthtouxiang.jpg");
    }
    if (this.$store.state.userMsg != "") {
      this.getUesr();
    }
    if (this.$store.state.userMsg.user_id) {
      this.hongbaoC = this.$store.state.hongbaoLength;
    }
  },
  methods: {
    // 获取用户信息
    getUesr() {
      loadingInstance = Loading.service({
        fullscreen: true,
        text: "拼命加载中……"
      });
      const api =
        "https://elm.cangdu.org/v1/user/" + this.$store.state.userMsg.user_id;
      this.$http({
        url: api,
        method: "get"
      }).then(res => {
        this.imgU = "https://elm.cangdu.org/img/" + res.data.avatar;
        loadingInstance.close();
      });
    },
    goLogin() {
      if (this.useMsg) {
        this.$router.push({
          name: "fourth_info"
        });
      } else {
        this.$router.push({
          name: "login"
        });
      }
    },
    clickBack() {
      this.$router.back();
    },
    goyue() {
      this.$router.push({
        name: "myyue"
      });
    },
    goyouhui() {
      this.$router.push({
        name: "myyouhui"
      });
    },
    goDownloadEleme() {
      this.$router.push({
        name: "download_eleme"
      });
    },
    goThird() {
      this.$router.push({
        name: "third"
      });
    },
    goJifen() {
      this.$router.push({
        name: "jifen"
      });
    },
    goMyJifen() {
      this.$router.push({
        name: "myjifen"
      });
    },
    goFwCenter() {
      this.$router.push({
        name: "fwcenter"
      });
    },
    goVip() {
      this.$router.push({
        name: "eleme_vip"
      });
    }
  }
};
</script>
<style scoped>
/* 头部部分 */
#fourth_top {
  width: 100%;
  background-color: #3190e8;
  color: white;
  overflow: hidden;
}
#backimg {
  width: 0.15rem;
  margin: 0 0 0 0.1rem;
}
#fourth_top span {
  margin-left: 35%;
  font-size: 0.2rem;
  font-weight: bold;
}
#img1 {
  border-radius: 50%;
  width: 100%;
  width: 0.6rem;
  float: left;
  margin: 0.1rem 0 0.2rem 0.2rem;
}
#img2 {
  width: 0.3rem;
}
#top_login img:nth-of-type(2) {
  border: 1px solid red;
}
#top_login p:nth-of-type(1),
#img1 {
  margin-top: 0.3rem;
}
#top_login div:nth-of-type(2) {
  float: left;
}
#top_login p {
  margin-left: 0.1rem;
}
#top_login p:nth-of-type(1) {
  font-size: 0.2rem;
  font-weight: bold;
  margin-left: 0.15rem;
}
#img3 {
  width: 0.3rem;
  float: right;
  margin-top: 0.45rem;
  margin-right: 0.1rem;
}
.phone {
  font-size: 0.1rem;
  margin-bottom: 0.2rem;
}
/* 中间余额部分 */
#fourth_center {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.lis {
  width: 33.3%;
  border-left: 0.01rem solid #f5f5f5;
  background-color: white;
  margin-bottom: 0.1rem;
}
#fourth_center li:nth-of-type(1) {
  border-left: 0;
}
.lis p span:nth-of-type(1) {
  font-weight: bold;
  font-size: 0.3rem;
}
.lis p span:nth-of-type(2) {
  font-size: 0.1rem;
}
#span1 {
  color: orange;
  margin-left: 0.2rem;
}
#span2 {
  color: red;
  margin-left: 0.45rem;
}
#span3 {
  color: green;
  margin-left: 0.45rem;
}
.lis p:nth-of-type(2) {
  font-size: 0.1rem;
  margin-left: 0.35rem;
  margin-bottom: 0.1rem;
}
/* 中间订单积分部分 */
#fourth_thid {
  background-color: #fff;
}
#fourth_thid li {
  padding: 0.1rem 0.1rem;
  border-bottom: 0.01rem solid #f5f5f5;
}
#fourth_thid li img:nth-of-type(1) {
  margin-left: 0.1rem;
  width: 0.25rem;
}
#fourth_thid li img:nth-of-type(2) {
  float: right;
  width: 0.07rem;
  margin-top: 0.09rem;
}
#fourth_thid li span {
  font-size: 0.15rem;
  margin-left: 0.1rem;
}
#fourth_thid li:nth-of-type(3) {
  border: 0;
}
/* 第四部分 */
#fourth_four {
  margin-top: 0.1rem;
  width: 100%;
  background-color: #fff;
}
#fourth_four li {
  padding: 0.1rem 0.1rem;
  border-bottom: 0.01rem solid #f5f5f5;
}
#fourth_four li img:nth-of-type(1) {
  width: 0.25rem;
  margin-left: 0.1rem;
}
#fourth_four li img:nth-of-type(2) {
  float: right;
  width: 0.07rem;
  margin-top: 0.1rem;
}
#fourth_four li span {
  margin-left: 0.1rem;
  font-size: 0.15rem;
}
#fourth_four li:nth-of-type(2) {
  border: 0;
  margin-bottom: 1rem;
}
</style>



