// 兑换红包
<template>
  <div id="bc">
    <!-- 头部 -->
    <div id="com_blue">
      <img @click="goBack()" src="../imgs/back.png" alt>
      <span>兑换红包</span>
    </div>
    <!-- 底部 -->
    <div id="hongbao_bot">
      <input type="text" id="code" placeholder="请输入兑换码" v-model="redeemcode" @input="inp()">
      <br>
      <input type="text" maxlength="4" placeholder="验证码" @input="inp()" v-model="redeemcode1">
      <span>
        <img :src="showCode&&showCode" alt>
      </span>
      <div id="dingwei">
        <span>看不清</span>
        <p @click="getCode()">换一张</p>
      </div>
      <button :style={backgroundColor:bc} @click="testa()">兑换</button>
    </div>
     <!-- 弹框 -->
      <div id="box" v-if="showtool">
        <img src="../imgs/tishi.jpg" alt>
        <p>{{import1}}</p>
        <button @click="closeTool()">确认</button>
      </div>
  </div>
</template>
<script>
var code=document.getElementById("code");
export default {
  name: "hongbao_left",
  data() {
    return {
      showCode: "",
      redeemcode:"",
      redeemcode1:"",
      showtool:false,
      import1:"",
      bc:"#ddd",
      inp1:"",
      inp2:""
    };
  },
  created() {
    this.getCode();
    this.inp();
  },
  methods: {
    goBack() {
      this.$router.back();
    },
    getCode() {
      const api = "https://elm.cangdu.org/v1/captchas";
      this.$http({
        url: api,
        method: "post",
        withCredentials: true
      }).then(res => {
        this.showCode = res.data.code;
      });
    },
    // 兑换红包
    exchange(){
         const api = "https://elm.cangdu.org/v1/users/"+this.$store.state.userMsg.user_id+"/hongbao/exchange";
      this.$http({
        url: api,
        method: "post",
        data:{
          user_id:this.$store.state.userMsg.user_id,
          exchange_code:this.redeemcode,
          captcha_code:this.showCode
        }
      }).then(res => {
        if(res.data.message){
          this.showtool=true;
          this.import1=res.data.message;
        }
      });
    },

    testa(){
      if(this.redeemcode!=""&&this.redeemcode1!=""){
        this.exchange();
      }
    },

    closeTool(){
      this.showtool=false;
    },
    // 判断输入框是否有值
    inp(){
      if(this.redeemcode!=""&&this.redeemcode1.length==4){
        this.bc="#4cd964"
      }else{
        this.bc="#ddd"
      }
    }
  },
};
</script>

<style scoped>

/* 底部 */
#hongbao_bot {
  width: 92%;
  margin-left: 4%;
  text-align: center;
  margin-top: 0.1rem;
}
#hongbao_bot input {
  border-radius: 0.05rem;
  border: 0;
  font-size: 0.18rem;
  padding: 0.1rem 0 0.1rem 0.05rem;
}
#hongbao_bot input:nth-of-type(1) {
  width: 98%;
}
#hongbao_bot input:nth-of-type(2) {
  width: 55%;
  float: left;
  margin-top: 0.15rem;
}
/* 验证码 */
#hongbao_bot > span {
  margin-top: 0.15rem;
  height: 0.44rem;
  width: 29%;
  float: right;
  font-size: 0.25rem;
  padding-right: 0.41rem;
  background-color: #fff;
  border-radius: 0.05rem;
}
#hongbao_bot > span img {
  width: 0.8rem;
}
/* 定位元素 */
#dingwei {
  position: absolute;
  right: 0.2rem;
  top: 1.16rem;
  font-size: 0.14rem;
}
#dingwei p {
  color: #3b95e9;
}
#hongbao_bot button {
  width: 100%;
  margin-top: 0.15rem;
  padding: 0.1rem 0 0.1rem 0;
  font-size: 0.15rem;
  border: 0;
  color: #fff;
  border-radius: 0.05rem;
}
</style>



