// 兑换会员
<template>
  <div id="bc">
    <!-- 头部 -->
    <div id="com_blue">
      <img @click="backClick()" src="../imgs/back.png" alt>
      <span>我的优惠</span>
    </div>
    <!-- 中间输入框部分 -->
    <div id="exVip_cen">
      <p>成功兑换后将关联到当前账号: {{$store.state.userMsg.username}}</p>
      <input type="text" maxlength="10" placeholder="请输入10位卡号" v-model="inp1" @input="change()">
      <input type="text" maxlength="6" placeholder="请输入6位卡密" v-model="inp2" @input="change()">
      <button @click="box()" :style="{backgroundColor:color}">兑换</button>
    </div>
    <!-- 底部温馨提示部分 -->
    <div id="exVip_bot">
      <p>————温馨提示————</p>
      <p>新兑换的会员服务，权益以「会员说明」为准。</p>
      <p>月卡: 30次减免配送费。</p>
      <p>季卡: 90次减免配送费。</p>
      <p>年卡: 360次减免配送费。</p>
      <p>*仅限蜂鸟专送订单，每日最多减免3单，每单最高减免4元(一个月按31天计算)</p>
    </div>
    <!-- 弹框 -->
      <div id="box" v-if="showtool">
        <img src="../imgs/tishi.jpg" alt>
        <p>{{import1}}</p>
        <button @click="closeTool()">确认</button>
      </div>
  </div>
</template>
<script>
export default {
  name: "exchange_vip",
  data() {
    return {
      inp1: "",
      inp2: "",
      color: "#ccc",
      showtool:false,
      import1:""
    };
  },
  created() {
    this.change();
  },
  methods: {
    backClick() {
      this.$router.back();
    },
    change() {
      if (this.inp1.length == 10 && this.inp2.length == 6) {
        this.color = "red";
      } else {
        this.color = "#ccc";
      }
    },
    box(){
        if(this.inp1==""||this.inp2==""){
            this.showtool=true;
            this.import1="请输入卡号"
        }else{
            this.showtool=true;
            this.import1="无效卡号"
        }
    },
    closeTool(){
        this.showtool=false;
    }
  }
};
</script>
<style scoped>
/* 中间输入框部分 */
#exVip_cen p {
  margin: 0.01rem 0 0.1rem 0.1rem;
  font-size: 0.15rem;
}
#exVip_cen input {
  box-sizing: border-box;
  width: 100%;
  padding: 0.1rem;
  font-size: 0.15rem;
  border: 0;
  border-bottom: 0.01rem solid #ddd;
}
#exVip_cen button {
  width: 92%;
  margin-left: 4%;
  text-align: center;
  color: white;
  font-size: 0.15rem;
  margin-top: 0.1rem;
  padding: 0.1rem;
  border: 0;
  border-radius: 0.05rem;
}
/* 底部提示部分 */
#exVip_bot {
  margin: 0.1rem 0.25rem 0 0.35rem;
  color: gray;
}
#exVip_bot p {
  font-size: 0.13rem;
  padding-top: 0.05rem;
}
#exVip_bot p:nth-of-type(1) {
  text-align: center;
  font-size: 0.16rem;
  margin-bottom: 0.1rem;
}
</style>



