<template>
  <div id="shop">
    <div id="shopid_top">
      <div id="shopid_top_show">
        <div id="shopid_top_show_img">
          <img :src="'https://elm.cangdu.org/img/'+datas.image_path">
        </div>
        <!-- 信息展示部分 -->
        <div id="shopid_top_show_infor">
          <p>{{datas.name}}</p>
          <p>
            <!-- <span>{{datas.piecewise_agent_fee.tips}}</span> -->
            <span>商家配送/分钟送达/</span>
          </p>
          <p>
            <span>公告：</span>
            <span>{{datas.promotion_info}}</span>
          </p>
        </div>
        <div @click="goback()" id="leftimg">
          <img src="../imgs/back.png" alt>
        </div>
        <div @click="goto()" id="rightimg">
          <img src="../imgs/youjiantou.png" alt>
        </div>
      </div>
    </div>

    <!-- 商品和公告的展示路径 -->
    <router-view></router-view>
  </div>
</template>
<script>
export default {
  name: "shopshow",
  data() {
    return {
      datas: ""
    };
  },
  created() {
    this.getdatas();
  },
  methods: {
    goback() {
      //返回上一级
      this.$router.push({
        name:"first"
      });
    },
    goto() {
      this.$router.push({
        name: "shopdetails"
      });
    },
    getdatas() {
      // 获取vuex中的全局资源
      const api =
        "https://elm.cangdu.org/shopping/restaurant/" +
        this.$store.state.shopinformationId;
      this.$http({
        url: api,
        method: "get",
        data: {
          //post的请求参数
        }
      }).then(res => {
        //res是请求返回的数据
        this.datas = res.data;
        // console.log(res.data);
      });
    }
  }
};
</script>
<style scoped>
#shop {
  height: 100vh;
  overflow: hidden;
}
#shopid_top {
  width: 100%;
  height: 1rem;
  background-color: grey;
  color: white;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 5;
  /* line-height: 1rem; */
}
#shopid_top_show_img {
  float: left;
  margin-left: 0.3rem;
  margin-top: 0.15rem;
}
#shopid_top_show_img img {
  width: 0.7rem;
}

#shopid_top_show_infor {
  float: left;
  margin-left: 0.07rem;
  margin-top: 0.13rem;
}
#shopid_top_show_infor p {
  margin-bottom: 0.07rem;
}
#shopid_top_show_img {
  position: relative;
}
#leftimg {
  position: absolute;
  left: 0.05rem;
  top: 0.35rem;
}
#leftimg img {
  width: 0.17rem;
}
#rightimg {
  position: absolute;
  right: 0;
  top: 0.25rem;
}
#rightimg img {
  height: 0.45rem;
}
</style>




