// 开发票
<template>
  <div>
    <!-- 头部 -->
    <div id="com_blue">
      <img @click="backClick()" src="../imgs/back.png" alt>
      <span>我的优惠</span>
    </div>
    <!-- 底部 -->
    <div id="fapiao_bot">
      <img src="../imgs/xiaoren.png" alt>
      <p>没有购买记录</p>
    </div>
  </div>
</template>
<script>
export default {
  name: "vip_fapiao",
  methods: {
    backClick() {
      this.$router.back();
    }
  }
};
</script>
<style scoped>
#fapiao_bot {
  margin-top: 1rem;
  text-align: center;
}
#fapiao_bot img {
  width: 1.8rem;
}
#fapiao_bot p {
  font-size: 0.15rem;
  margin-top: 0.1rem;
  color: gray;
}
</style>



