// 在线购买
<template>
  <div id="bc">
    <!-- 头部 -->
    <div id="com_blue">
      <img @click="backClick()" src="../imgs/back.png" alt>
      <span>在线购买</span>
    </div>
    <!-- 中间剩余时间 -->
    <div id="timeDowm">
      <p>支付剩余时间</p>
      <p>0{{hour}}:{{minutes}}:{{seconds}}</p>
    </div>
    <!-- 底部切换支付方式 -->
    <p id="choose">选择支付方式</p>
    <div id="buy_bot">
        <p><img src="../imgs/zhifubao.png" alt=""><span>支付宝</span><span @click="Show2()"><img v-if="imgShow" src="../imgs/icon-test.png" alt=""><img v-else src="../imgs/icon-test2.png" alt=""></span></p>
        <p><img src="../imgs/weixin2.png" alt=""><span>微信</span><span @click="Show()"><img  v-if="imgShow2" src="../imgs/icon-test2.png" alt=""><img v-else src="../imgs/icon-test.png" alt=""></span></p>
        <button @click="buyBtn()">确认支付</button>
    </div>
    <!-- 弹框 -->
    <div id="box" v-if="showtool">
      <img src="../imgs/tishi2.jpg" alt>
      <p>{{count}}</p>
      <button @click="closeTool()">确认</button>
    </div>
  </div>
</template>
<script>
export default {
  name: "buy_vip",
  data() {
    return {
        imgShow:true,
        imgShow2:true,
      showtool: true,
      hour: 0,
      minutes: 15,
      seconds: 0,
      count: "暂不开放支付功能"
    };
  },
  created() {
    this.timer();
  },
  methods: {
      Show(){
          this.imgShow=false
          this.imgShow2=false
      },
      Show2(){
          this.imgShow=true
          this.imgShow2=true
      },
    backClick() {
      this.$router.back();
    },
    closeTool() {
      this.showtool = false;
    },
    num(n) {
      return n < 10 ? "0" + n : "" + n;
    },
    timer() {
        
      var _this = this;
      var time = window.setInterval(function() {
        if (_this.seconds == 0 && _this.minutes != 0) {
          _this.seconds = 59;
          _this.minutes--;
        } else if (_this.minutes == 0 && _this.seconds == 0) {
          _this.seconds = 0;
          window.clearInterval(time);
          _this.showtool = true;
          _this.count = "支付超时";
        } else {
          _this.seconds--;
        }
      }, 1000);
    },
    buyBtn(){
        this.count="当前环境无法支付，请打开官方APP进行付款";
        this.showtool=true;
    }
  }
};
</script>
<style scoped>
#box {
  background-color: #fff;
}
/* 倒计时 */
#timeDowm {
  width: 100%;
  text-align: center;
  background-color: #fff;
  padding: 0.25rem 0 0.2rem 0;
}
#timeDowm p:nth-of-type(1) {
  font-size: 0.15rem;
  color: gray;
}
#timeDowm p:nth-of-type(2) {
  font-size: 0.3rem;
}
/* 选择支付方式 */
#choose{
    font-size: 0.15rem;
    margin: 0.1rem 0 0.1rem 0.1rem;
}
#buy_bot{
    background-color: #fff;
}
#buy_bot>p{
    border-bottom: 0.01rem solid #ddd;
}
#buy_bot>p img:nth-of-type(1){
    margin: 0.15rem 0.05rem 0.15rem 0.15rem;
    width: 0.5rem;
}
#buy_bot>p span{
    font-size: 0.15rem;
}
#buy_bot>p span:nth-of-type(2) img{
    margin: 0.23rem 0.1rem 0 0;
    float: right;
    width: 0.25rem;
}
#buy_bot button{
    margin-top: 0.1rem;
    width: 92%;
    margin-left: 4%;
    background-color: #4cd964;
    border: 0;
    color: white;
    padding: 0.1rem;
    font-size: 0.15rem;
    border-radius: 0.05rem;
}
</style>



