// 子路由红包部分
<template>
  <div id="bc">
    <!-- 中间路由部分 -->
    <div id="youhui_center">
      <span @click="goHongbao()">
        红包
        <p></p>
      </span>
      <span @click="goDaijin()">商家代金券</span>
    </div>
    <!-- 子路由部分 -->
    <div id="hongbao">
      <div id="hongbao_top">
        <span>
          有
          <span>{{hongbaoNum}}</span>个红包即将过期
        </span>
        <span @click="goHongbaoEx()">
          <span>？</span>红包说明
        </span>
      </div>
      <ul id="hongbao_ul">
        <li v-for="(v,i) in data" :key="i">
          <span>
            ￥
            <span>{{v.amount}}</span>
            <p>{{v.description_map.sum_condition}}</p>
          </span>
          <span>
            <p>{{v.name}}</p>
            <p>{{v.description_map.validity_periods}}</p>
            <p>{{v.description_map.phone}}</p>
          </span>
          <span>{{v.description_map.validity_delta}}</span>
          <p>{{v.limit_map?v.limit_map.restaurant_flavor_ids:""}}</p>
        </li>
      </ul>
      <p @click="goHisHongbao()">
        查看历史红包
        <img src="../imgs/right.png" alt>
      </p>
    </div>
    <!-- 弹框 -->
    <div id="box" v-if="showtool">
      <img src="../imgs/tishi2.jpg" alt>
      <p>{{import1}}</p>
      <button @click="closeTool()">确认</button>
    </div>
    <!-- 底部部分 -->
    <div id="youhui_bottom">
      <span @click="goLeft()">兑换红包</span>
      <span @click="goRight()">推荐红包</span>
    </div>
  </div>
</template>
<script>
import { Loading } from "element-ui";
let loadingInstance;
export default {
  name: "youhui_hongbao",
  data() {
    return {
      userId: "",
      hongbaoNum: "",
      data: [],
      showtool: false,
      import1: "请先登录"
    };
  },
  created() {
    if (this.$store.state.userMsg.user_id) {
      loadingInstance = Loading.service({
        fullscreen: true,
        text: "拼命加载中……"
      });
      this.userId = this.$store.state.userMsg.user_id;
      this.getHongbao();
    } else {
      this.showtool = true;
    }
  },
  methods: {
    // 关闭弹框
    closeTool() {
      this.showtool = false;
    },
    //   获取红包数据
    getHongbao() {
      if (this.userId) {
        const api =
          "https://elm.cangdu.org/promotion/v2/users/" +
          this.userId +
          "/hongbaos?limit=20&offset=0";
        this.$http({
          url: api,
          method: "get"
        }).then(res => {
          this.data = res.data;
          this.hongbaoNum = this.data.length;
          this.$store.commit("hongbaoC", this.data.length);
          loadingInstance.close();
        });
      }
    },
    goDaijin() {
      this.$router.push({
        name: "youhui_daijin"
      });
    },
    goHongbao() {
      this.$router.push({
        name: "youhui_hongbao"
      });
    },
    goHongbaoEx() {
      this.$router.push({
        name: "hongbao_ex"
      });
    },
    goHisHongbao() {
      this.$router.push({
        name: "his_hongbao"
      });
    },
    goLeft() {
      this.$router.push({
        name: "hongbao_left"
      });
    },
    goRight() {
      this.$router.push({
        name: "hongbao_right"
      });
    }
  }
};
</script>
<style scoped>
#bc{
  overflow: hidden;
}
#box{
  background-color: #fff;
}
/* 中间路由部分 */
#youhui_center {
  position: fixed;
  top: 0.42rem;
  left: 0;
  width: 100%;
  padding: 0.1rem 0 0.1rem 0;
  background-color: #fff;
}
#youhui_center span {
  display: inline-block;
  width: 49%;
  text-align: center;
  font-size: 0.18rem;
}
#youhui_center span:nth-child(1) {
  color: #3190e8;
}
#youhui_center > span > p {
  width: 0.3rem;
  height: 0.02rem;
  background-color: blue;
  margin-left: 0.75rem;
}

/* 中间红包部分 */
#hongbao_top {
  margin-top: 0.9rem;
  padding: 0.1rem;
}
#hongbao_top {
  font-size: 0.12rem;
  color: gray;
}
#hongbao_top > span:nth-child(1) span:nth-child(1) {
  color: #ff5340;
}
#hongbao_top > span:nth-child(2) {
  float: right;
  color: #3190e8;
}
#hongbao_top > span:nth-child(2) span:nth-child(1) {
  width: 0.1rem;
  height: 0.1rem;
  background-color: #3190e8;
  border-radius: 50%;
  text-align: center;
  color: white;
  padding-left: 0.05rem;
}
#hongbao > p {
  width: 100%;
  text-align: center;
  font-size: 0.12rem;
  color: gray;
  margin-top: 0.2rem;
}
#hongbao img {
  margin-left: 0.05rem;
  width: 0.06rem;
  margin-top: -0.02rem;
}
/* 红包 */
#hongbao_ul {
  overflow: hidden;
}
#hongbao_ul li {
  background-color: #fff;
  border-top: 0.07rem solid red;
  margin-top: 0.1rem;
  width: 90%;
  margin-left: 5%;
  overflow: hidden;
}

#hongbao_ul li > span:nth-of-type(1) {
  border-right: 0.01rem dotted #ddd;
  font-size: 0.25rem;
  color: red;
  padding: 0.1rem 0.15rem 0.1rem 0.05rem;
  float: left;
}
#hongbao_ul li > span:nth-of-type(1) p:nth-of-type(1) {
  font-size: 0.13rem;
  color: gray;
}
#hongbao_ul li > span:nth-of-type(2) {
  padding: 0.1rem 0 0.1rem 0.05rem;
  float: left;
  width: 1.7rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
#hongbao_ul li > span:nth-of-type(2) p:nth-of-type(1) {
  font-size: 0.18rem;
}
#hongbao_ul li > span:nth-of-type(2) p:nth-of-type(2),
#hongbao_ul li > span:nth-of-type(2) p:nth-of-type(3) {
  color: gray;
  font-size: 0.1rem;
}
#hongbao_ul li > span:nth-of-type(3) {
  float: right;
  padding: 0.1rem 0.05rem 0 0;
  font-size: 0.18rem;
  color: red;
}
#hongbao_ul li > p {
  width: 100%;
  padding-top: 0.02rem;
  float: left;
  font-size: 0.1rem;
  color: gray;
  background-color: #f5f5f5;
}
/* 底部部分 */
#youhui_bottom {
  width: 100%;
  background-color: #fff;
  position: fixed;
  bottom: 0;
  left: 0;
}
#youhui_bottom span {
  display: inline-block;
  width: 49%;
  text-align: center;
  font-size: 0.18rem;
  border-left: 0.01rem solid #ddd;
  padding: 0.1rem 0 0.1rem;
}
#youhui_bottom span:nth-child(1) {
  border: 0;
}
</style>



