// 修改密码
<template>
  <div id="bc">
    <!-- 重置密码 -->
    <div id="com_blue">
      <img @click="backClick()" src="../imgs/back.png" alt />
      <span>重置密码</span>
    </div>
    <!-- 输入框部分 -->
    <div id="change_inp">
      <input type="text" placeholder="账号" v-model="useName" />
      <input type="text" placeholder="旧密码" v-model="oldpassWord" />
      <input type="text" placeholder="请输入新密码" v-model="newpassword" />
      <input type="text" placeholder="请确认密码" v-model="confirmpassword" />
      <div id="change_code">
        <input type="text" placeholder="验证码" v-model="codeNumber" />
        <span>
          <img :src="showCode&&showCode" alt />
        </span>
        <span>看不清</span>
        <span @click="getCode()">换一张</span>
      </div>
    </div>
    <button id="but" @click="changePass()">确认修改</button>
    <!-- 弹框 -->
    <div id="box" v-if="showTool">
      <img src="../imgs/tishi.jpg" alt />
      <p>{{import1}}</p>
      <button @click="closeTool()">确认</button>
    </div>
  </div>
</template>
<script>
export default {
  name: "change_pass",
  data() {
    return {
      showCode: "",
      useName: "",
      oldpassWord: "",
      newpassword: "",
      confirmpassword: "",
      codeNumber: "",
      import1: "",
      showTool: false
    };
  },
  created() {
    this.getCode();
  },
  methods: {
    getCode() {
      const api = "https://elm.cangdu.org/v1/captchas";
      this.$http({
        url: api,
        method: "post",
        withCredentials: true
      }).then(res => {
        this.showCode = res.data.code;
      });
    },
    changePass() {
      if (!this.useName) {
        this.showTool = true;
        this.import1 = "请输入正确的账号";
      } else if (!this.oldpassWord) {
        this.showTool = true;
        this.import1 = "请输入旧密码";
      } else if (!this.newpassword) {
        this.showTool = true;
        this.import1 = "请输入新密码";
      } else if (!this.confirmpassword) {
        this.showTool = true;
        this.import1 = "请输入确认密码";
      } else if (this.confirmpassword != this.newpassword) {
        this.showTool = true;
        this.import1 = "两次密码不一致";
      } else if (!this.codeNumber) {
        this.showTool = true;
        this.import1 = "请输入验证码";
      } else {
        const api = "https://elm.cangdu.org/v2/changepassword";
        this.$http({
          url: api,
          method: "post",
          withCredentials: true,
          data: {
            username: this.useName,
            oldpassWord: this.oldpassWord,
            newpassword: this.newpassword,
            confirmpassword: this.confirmpassword,
            captcha_code: this.codeNumber
          }
        }).then(res => {
          if (res.data.message) {
            this.showTool = true;
            this.import1 = res.data.message;
            this.getCode();
          } else {
            this.showTool = true;
            this.import1 = res.data.message;
            this.$router.back();
          }
        });
      }
    },
    backClick() {
      this.$router.back();
    },
    closeTool() {
      this.showTool = false;
    }
  }
};
</script>
<style scoped>
/* 输入框部分 */
#change_inp {
  margin-top: 0.2rem;
  border-top: 0.01rem solid #ddd;
}
#change_inp input {
  float: left;
  width: 100%;
  height: 0.5rem;
  padding: 0.2rem;
  font-size: 0.2rem;
  border: 0;
  border-bottom: 0.001rem solid #ddd;
  box-sizing: border-box;
}
/* 验证码部分 */
#change_code {
  position: relative;
}
#change_code span {
  position: absolute;
}
#change_code span:nth-of-type(1) {
  top: 2rem;
  right: 0.6rem;
  width: 1rem;
  height: 0.4rem;
}
#change_code img {
  width: 100%;
}
#change_code span:nth-of-type(2),
#change_code span:nth-of-type(3) {
  font-size: 0.13rem;
}
#change_code span:nth-of-type(2) {
  top: 2rem;
  right: 0.1rem;
}
#change_code span:nth-of-type(3) {
  top: 2.23rem;
  right: 0.1rem;
  color: blue;
}
/* 确认按钮 */
#but {
  margin-top: 0.3rem;
  margin-left: 3%;
  width: 94%;
  height: 0.4rem;
  font-size: 0.2rem;
  background-color: #4cd964;
  color: white;
  border: 0;
}
</style>



