// 新增地址
<template>
  <div id="bc">
    <!-- 头部 -->
    <div id="com_blue">
      <img @click="goBack()" src="../imgs/back.png" alt>
      <span>新增地址</span>
    </div>
    <!-- 中间输入部分 -->
    <div id="new_center">
      <input
        :style="{border:borderColor1}"
        type="text"
        placeholder="请填写你的姓名"
        v-model="name"
        @input="change1()"
        ref="type1"
      >
      <p v-if="show1">请填写你的姓名</p>
      <input
        :style="{border:borderColor}"
        type="text"
        :placeholder="place"
        @click="goSearchAdd()"
        v-model="address"
        readonly
        unselectable="on"
      >
      <input
        :style="{border:borderColor2}"
        type="text"
        placeholder="请填写详细地址"
        v-model="addressEx"
        @input="change2()"
        ref="type2"
      >
      <p v-if="show2">请填写详细地址</p>
      <input
        :style="{border:borderColor3}"
        type="text"
        placeholder="请填写能够联系到您的手机号"
        v-model="phone"
        @input="change3()"
        ref="type3"
      >
      <p v-if="show3">手机号不能为空</p>
      <p v-if="show4">请输入正确的手机号</p>
      <input
        :style="{border:borderColor4}"
        type="text"
        placeholder="备用联系电话(选填)"
        v-model="oncePhone"
        @input="change4()"
        ref="type4"
      >
      <p v-if="show5">请输入正确的手机号</p>
    </div>
    <!-- 按钮 -->
    <button
      @click="(name!=''&&address!=''&&addressEx!=''&&phone!='')?addSuccess():openTool()"
      :style="{color:colorChange}"
    >新增地址</button>

    <!-- 弹框 -->
    <div id="box" v-if="showtool">
      <img src="../imgs/tishi.jpg" alt>
      <p>{{import1}}</p>
      <button @click="closeTool()">确认</button>
    </div>
  </div>
</template>
<script>
export default {
  nameL: "newAddress",
  data() {
    return {
      show1: false,
      show2: false,
      show3: false,
      show4: false,
      show5: false,
      import1: "地址信息错误",
      showtool: false,
      colorChange: "white",
      name: "",
      address: "",
      addressEx: "",
      phone: "",
      oncePhone: "",
      borderColor: "0.01rem solid #ddd",
      borderColor1: "0.01rem solid #ddd",
      borderColor2: "0.01rem solid #ddd",
      borderColor3: "0.01rem solid #ddd",
      borderColor4: "0.01rem solid #ddd",
      userid: "",
      name2: "",
      place: "小区/写字楼/学校等",
      geohash: "",
      sex: "1",
      tag_type: "2",
      tag: "你好"
    };
  },
  created() {
    this.userid = this.$store.state.userMsg.user_id;
    this.address = this.$store.state.address;
    if (this.address) {
      this.place = this.address;
    } else {
      this.place = "小区/写字楼/学校等";
    }
    this.geohash = this.$store.state.geohash;
    console.log(this.$store.state.geohash);
  },
  methods: {
    goBack() {
      this.$router.push({
        name: "Aaddress"
      });
    },
    goSearchAdd() {
      this.$router.push({
        name: "searchAddress"
      });
    },
    closeTool() {
      this.showtool = false;
    },
    openTool() {
      this.showtool = true;
    },
    // 加地址
    addSuccess() {
      const api =
        "https://elm.cangdu.org/v1/users/" +
        this.$store.state.userMsg.user_id +
        "/addresses";
      this.$http({
        url: api,
        method: "post",
        data: {
          user_id: this.$store.state.userMsg.user_id,
          geohash: this.$store.state.geohash,
          sex: this.sex,
          tag: this.tag,
          tag_type: this.tag_type,
          address: this.address,
          address_detail: this.addressEx,
          name: this.name,
          phone: this.phone,
          phone_bk: this.oncePhone
        }
      }).then(res => {
        this.$router.push({
          name: "Aaddress"
        });
      });
    },
    // 判断部分，红字边框颜色变化
    change1() {
      if (this.$refs.type1.value.length == 0) {
        this.borderColor1 = "0.01rem solid red";
        this.show1 = true;
      } else {
        this.show1 = false;
        this.borderColor1 = "0.01rem solid #ddd";
      }
    },
    change2() {
      if (this.$refs.type2.value == "") {
        this.borderColor2 = "0.01rem solid red";
        this.show2 = true;
      } else {
        this.show2 = false;
        this.borderColor2 = "0.01rem solid #ddd";
      }
    },
    change3() {
      if (this.$refs.type3.value == "") {
        this.borderColor3 = "0.01rem solid red";
        this.show4 = false;
        this.show3 = true;
      } else if (!/^1[3456789]\d{9}$/.test(this.$refs.type3.value)) {
        this.borderColor3 = "0.01rem solid red";
        this.show4 = true;
        this.show3 = false;
      } else {
        this.borderColor3 = "0.01rem solid #ddd";
        this.show3 = false;
        this.show4 = false;
      }
    },
    change4() {
      if (!/^1[3456789]\d{9}$/.test(this.$refs.type4.value)) {
        this.borderColor4 = "0.01rem solid red";
        this.show5 = true;
      } else {
        this.borderColor4 = "0.01rem solid #ddd";
        this.show5 = false;
      }
      if (this.$refs.type4.value == "") {
        this.borderColor4 = "0.01rem solid #ddd";
        this.show5 = false;
      }
    }
  }
};
</script>
<style scoped>
#new_center {
  width: 100%;
  background-color: #fff;
  margin-top: 0.1rem;
}
#new_center input {
  width: 92%;
  margin-left: 2%;
  padding: 0.05rem 0 0.05rem 0.08rem;
  margin-top: 0.1rem;
  background-color: #ddd;
  /* border: 0.01rem solid #ddd; */
  border-radius: 0.05rem;
  font-size: 0.14rem;
}
#new_center input:nth-last-child(1) {
  margin-bottom: 0.1rem;
}
#new_center p {
  color: red;
  margin-left: 0.1rem;
  font-size: 0.13rem;
}
button {
  width: 92%;
  margin-left: 4%;
  padding: 0.1rem 0 0.1rem 0;
  background-color: #4cd964;
  border: 0;
  border-radius: 0.05rem;
  font-size: 0.14rem;
  margin-top: 0.1rem;
}
</style>



