// 登录注册界面
<template>
  <div id="bc">
    <!-- 登录头部 -->
    <div id="com_blue">
      <img @click="clickBack()" src="../imgs/back.png" alt>
      <span>密码登录</span>
    </div>
    <!-- 登录输入框 -->
    <div id="login_inp">
      <input type="text" v-model="useName" placeholder="账号">
      <div id="login_password">
        <input type="password" v-model="passWord" v-if="showPassword" placeholder="密码">
        <input v-model="passWord" type="text" v-else placeholder="密码">
        <div id="password_hid" @click="showPassword=!showPassword">
          <el-switch
            v-model="value"
            active-text="abc..."
            active-color="#13ce66"
            inactive-color="gray"
          ></el-switch>
        </div>
      </div>
      <!-- 验证码 -->
      <div id="login_code">
        <input type="text" v-model="codeNumber" placeholder="验证码">
        <br>
        <span>
          <img :src="showCode&&showCode" alt>
        </span>
        <span>看不清</span>
        <span @click="getCode()">换一张</span>
      </div>
      <!-- 红字部分 -->
      <div id="lint">
        <p>温馨提示:未注册过的账号,登录时将自动注册</p>
        <p>注册过的用户可凭账号密码登录</p>
      </div>
      <button id="but" @click="login()">登录</button>
      <button id="reset" @click=" changePass()">重置密码?</button>
      <!-- 弹框 -->
      <div id="box" v-if="showtool">
        <img src="../imgs/tishi.jpg" alt>
        <p>{{import1}}</p>
        <button @click="closeTool()">确认</button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "login",
  data() {
    return {
      value: false,
      showPassword: true,
      showtool: false,
      showCode: "",
      useName: "",
      passWord: "",
      codeNumber: "",
      import1: ""
    };
  },
  created() {
    this.getCode();
  },
  methods: {
    getCode() {
      const api = "https://elm.cangdu.org/v1/captchas";
      this.$http({
        url: api,
        method: "post",
        withCredentials: true
      }).then(res => {
        this.showCode = res.data.code;
      });
    },

    login() {
      if (!this.useName) {
        this.showtool = true;
        this.import1 = "请输入手机号/邮箱/用户名";
        return;
      } else if (!this.passWord) {
        this.showtool = true;
        this.import1 = "请输入密码";
        return;
      } else if (!this.codeNumber) {
        this.showtool = true;
        this.import1 = "请输入验证码";
        return;
      } else {
        const api = "https://elm.cangdu.org/v2/login";
        this.$http({
          url: api,
          method: "post",
          withCredentials: true,
          data: {
            username: this.useName,
            password: this.passWord,
            captcha_code: this.codeNumber
          }
        }).then(res => {
          if (res.data.message) {
            this.showtool = true;
            this.import1 = res.data.message;
            this.getCode();
          } else {
            this.$store.commit("getUsers", res.data);
            this.$router.back();
          }
        });
      }
    },

    changePass() {
      this.$router.push({
        name: "change_pass"
      });
    },
    clickBack() {
      this.$router.back();
    },
    closeTool() {
      this.showtool = false;
    }
  }
};
</script>
<style scoped>
/* 输入框 */
#login_inp {
  margin-top: 0.2rem;
  border-top: 0.01rem solid #ddd;
}
#login_inp input {
  float: left;
  width: 100%;
  height: 0.5rem;
  padding: 0.2rem;
  font-size: 0.2rem;
  border: 0;
  border-bottom: 0.001rem solid #ddd;
  box-sizing: border-box;
}
/* 密码切换部分 */
#login_password {
  position: relative;
}
#password_hid {
  position: absolute;
  top: 0.65rem;
  right: 0.1rem;
}
#password_hid el-switch {
  width: 1rem;
}
/* 验证码部分 */
#login_code {
  position: relative;
}
#login_code span {
  position: absolute;
}
#login_code span:nth-of-type(1) {
  top: 1.05rem;
  right: 0.6rem;
  width: 1rem;
  height: 0.4rem;
}
#login_code img {
  width: 100%;
}
#login_code span:nth-of-type(2),
#login_code span:nth-of-type(3) {
  font-size: 0.13rem;
}
#login_code span:nth-of-type(2) {
  top: 1.05rem;
  right: 0.1rem;
}
#login_code span:nth-of-type(3) {
  top: 1.2rem;
  right: 0.1rem;
  color: blue;
}
/* 红字部分 */
#lint p {
  color: red;
  margin-left: 0.1rem;
  margin-top: 0.1rem;
  font-size: 0.1rem;
}
/* 登录按钮 */
#but {
  margin-top: 0.1rem;
  margin-left: 3%;
  width: 94%;
  font-size: 0.2rem;
  background-color: #4cd964;
  color: white;
  border: 0;
}
/* 重置按钮 */
#reset {
  color: blue;
  background-color: #fff;
  border: 0;
  font-size: 0.15rem;
  float: right;
  margin-top: 0.3rem;
  margin-right: 0.1rem;
}

/* Request URL: https://elm.cangdu.org/eus/v1/users/34111/avatar
Request Method: POST

file: (binary)*/
</style>



