// 代金券说明
<template>
    <div>
        <div id="com_blue">
      <img @click="goBack()" src="../imgs/back.png" alt>
      <span>代金券说明</span>
    </div>
    <div id="ex_bot">
        <p>Q1:什么是商家代金券?</p>
        <span>商家代金券是指由商家自己发放代金券，只限在指定的商家使用，可根据条件抵扣相应金额。</span>
        <p>Q2:怎么获得商家代金券?</p>
        <span>进入有「进店领券」或「下单返券」标示的商家即有机会获得代金券。
「下单返券」需要在指定商家完成满足返券金额要求的订单后才会返还，代金券可在下次下单时使用。</span>
        <p>Q3:商家代金券使用条件</p>
        <span>商家代金券仅限在指定商家使用</span><br>
        <span>商家代金券仅限在线支付使用</span><br>
        <span>每个订单只能使用一张商家代金券，不可与其他代金券叠加使用</span>
    </div>
    </div>
</template>
<script>
export default {
    name:"daijin_ex",
    methods: {
        goBack(){
            this.$router.back()
        }
    },
}
</script>
<style scoped>

</style>



