<template>
  <div>
    <div id="food_top">
      <span @click="goback()">
        <img src="../imgs/back.png" alt>
      </span>
      <span>{{cityTitle}}</span>
    </div>
    <!-- 下拉列表 -->
    <div class="top_b">
      <!-- 分类数据列表 -->
      <el-cascader
        placeholder="分类"
        class="fenlei"
        :options="fenleiOp"
        :props="fenleiParams"
        @change="handleChange1"
        :show-all-levels="false"
      ><template slot-scope="{ node, data }">
        <!-- 设置列表的图标 -->
        <img src="../imgs/SSS1.png" alt="">
<span>{{ data.name }}</span>
<span> ({{ data.count }}) </span>
</template></el-cascader>

      <!-- 排序数据列表 -->
      <el-cascader
        placeholder="排序"
        class="paixu"
        @change="handleChange2"
        :options="paixuoptions"
        clearable
      ></el-cascader>

      <!-- 筛选数据列表 -->
      <el-cascader 
      placeholder="筛选" 
      class="shaixuan" 
      :options="paixuoptions" 
      clearable>
      </el-cascader>
    </div>

    <!-- 父路由调用子路由的方法的方式 ref参数随便定义 -->
    <!-- <div>
      <router-view ref="infor"></router-view>
    </div> -->
    <!-- <router-view ref="infor"></router-view> -->
    <div id="food_bottom">
      <router-view ref="infor"></router-view>
    </div>
  </div>
</template>


<script>
export default {
  name: "Foods",
  data() {
    return {
      cityTitle: "", //存储由轮播图传递过来的地址
      fenid1:'',//存储分类数据id号
      fenid2:'',
      fenleiOp: [], //存储分类列表资源
      fenleiParams: {
        label: "name",
        value: "id",
        children: "sub_categories"
      },
      //   排序数据
      paixuoptions: [
        {
          label: "起送价",
          value: "1"
        },
        {
          label: "配送速度",
          value: "2"
        },
        {
          label: "评分",
          value: "3"
        },
        {
          label: "智能排序",
          value: "4"
        },
        {
          label: "距离最近",
          value: "5"
        },
        {
          label: "销量最高",
          value: "6"
        }
      ]
    };
  },
  created() {
    this.getrestList();
     this.cityTitle = this.$route.query.cityTitle;
     this.$store.commit
     ("getshopID",4);
    // this.getdatas();
  },
  methods: {
    goback() {
      //返回上一级
      this.$router.go(-1);
    },
    // 获取分类列表的资源
    getrestList() {
      const api = "https://elm.cangdu.org/shopping/v2/restaurant/category";
      this.$http.get(api).then(res => {
        // console.log(res.data);
        this.fenleiOp = res.data;
      });
    },
   
    //获取排序下拉列表id
    handleChange2(value) {
      this.$store.commit("getshopID",value[0]);
      this.$refs.infor.getdatas(this.name);
    },

    // 动态获得下拉列表的id
    handleChange1(value) {
      let a=value[0];
      let b=value[1];
      // this.$refs.infor.getdatas(this.name);
      for (let i = 0; i < this.fenleiOp.length; i++) {
          if (a== this.fenleiOp[i].id) {
            this.fenid1 = this.fenleiOp[i].name;
            // console.log(this.fenid1);
            for (let j = 0; j < this.fenleiOp[i].sub_categories.length; j++) {
              if (b == this.fenleiOp[i].sub_categories[j].id) {
                this.fenid2 = this.fenleiOp[i].sub_categories[j].name;
                // console.log(this.fenid2);
              }
            }
          }
      }
              // 分类数据传值  
              this.$store.commit("getfenleiID",{a:this.fenid1,b:this.fenid2});
              this.$refs.infor.getfenleidatas();
    },

  },
  
};
</script>

<style scoped>
#food_top {
  background-color: #3190e8;
  height: 0.4rem;
  text-align: center;
  line-height: 0.4rem;
  font-size: 0.2rem;
  color: white;
  /* position: fixed;
  left: 0;
  top: 0; */
}
#food_top img {
  height: 0.25rem;
}
#food_top span:nth-child(1) {
  float: left;
  padding-left: 0.1rem;
}
.fenlei,.el-input {
  width: 32.5%;
  margin-left: 0.2%;
  /* border-right: 1px solid gray; */
}
.el-input__inner{
  border-radius: 0;
}
.paixu {
  width: 33%;
}
.shaixuan{
  width: 32%;
}
#food_bottom{
  margin-top: -0.1rem;
}
.el-cascader-node__label img{
  width: 0.2rem;
}
.el-popper, .el-cascader__dropdown{
  height: 5rem;
}
.el-scrollbar__view, .el-cascader-menu__list{
  height: 5rem;
}
</style>



