export default {
  // 登录时的所有数据
  getUsers(state, a) {
    state.userMsg = a;
  },

  addAddress(state, a) {
    state.addAddress = a;
  },

  getSite(state, v) {
    // console.log(v);
    state.siteData = v.a;
    state.siteData1 = v.b;
    state.geohash = v.c;
  },
  addressSite(state, a) {
    state.address = a;
  },
  // 红包的长度
  hongbaoC(state, a) {
    state.hongbaoLength = a;
  },

  //  搜索到的餐馆id
  searchId(state, a) {
    state.searchId = a;
  },
  // 首页城市id，城市名
  searchId(state, v) {
    state.cityId = v.x;
    state.cityName=v.y;
  },
  // wwl
  getshopName(state, v) {
    state.cityId = v.a;
    state.cityaddress = v.b;
    state.cityname = v.c;
    state.cityadrs = v.d;
  },

  //获得经纬度的参数
  getLongitude(state, longitude) {
    state.longitude = longitude;
  },
  getLatitude(state, latitude) {
    state.latitude = latitude;
  },
  // 获取排序下标ID号
  getshopID(state, shopID) {//state是必须参数
    state.shopID = shopID;
  },
  // 获取分类数据资源
  getfenleiID(state, data) {//state是必须参数
    state.fendata1 = data.a;
    state.fendata2 = data.b;
  },
  //获取商铺ID为商家展示做准备
  getshopinformID(state, shopinformationId) {//state是必须参数
    state.shopinformationId = shopinformationId;
  },

  //获取购物车信息
  getfoodinf(state, datas) {//state是必须参数
    // console.log("获取购物车信息");
    // console.log(datas);
    state.gshopid = datas.id;
    state.gshopPr = datas.price;
    state.gshopNa = datas.name;
    //每种食品的数量
    state.index1 = (datas.index);
    // console.log(state.index1);
    // console.log("aaa");
    // console.log(state.att)
    // 将商品信息存储到数组中
    if (state.arrA.length == 0) {
      // console.log("数组为空时");
      state.arrA.push(datas);


    } else {
      var a = true;
      for (let i = 0; i < state.arrA.length; i++) {
        if (state.arrA[i].id == state.gshopid ) {
          state.arrA[i].index = state.index1;
          // console.log(state.arrA);
          a = false;
          // 删除清空的购物车数组元素
          if (state.index1<=0) {
            this.state.arrA.splice(i,1);
          }
        }
      }
      if (state.index1 < 0) {
        this.state.arrA.pop();
      }
      if (a) {
        state.arrA.push(datas);
        // console.log(state.arrA)
      }
      
    };
    //计算商品数量,和总价格
    state.arrNum = [];
    state.arrpr = [];
    for (let i = 0; i < state.arrA.length; i++) {
      state.gshopNum = 0;
      state.arrNum.push(state.arrA[i].index);
      state.arrpr.push(state.arrA[i].index * state.arrA[i].price)
      // console.log(state.arrNum);
      // console.log(state.arrpr);
    }
    // 计算数量总数
    state.gshopNum = 0;
    for (let i = 0; i < state.arrNum.length; i++) {
      state.gshopNum += state.arrNum[i]  
    }
    // 计算价格总数
    state.gshopAllPr = 0;
    for (let i = 0; i < state.arrpr.length; i++) {
      state.gshopAllPr += state.arrpr[i]  
    }

    // state.gshopid = datas.id;
    // state.gshopPr = datas.price;
    // state.gshopNa = datas.name;
    // //每种食品的数量
    // state.index1 = (datas.index);
    if (state.gshopid==-1&&state.gshopPr==-1&&state.gshopNa==-1&&state.index1==-1) {
      state.arrNum = [];
      state.arrpr = [];
      state.gshopNum = 0;
      state.gshopAllPr = 0;
      state.arrA = [];
    }

  },

}


