// 我的积分
<template>
    <div id="bc">
         <!-- 头部 -->
        <div id="com_top">
            <img @click="backClick()" src="../imgs/back.png" alt="">
            <span>我的积分</span>
            <div id="now">
                <span>当前积分</span>
                <div>
                    <span>？</span>
                    <span @click="goEx()">积分说明</span>
                </div>
                <p>0分</p>
                <button @click="openTool()" id="but">积分兑换商品</button>
            </div>
        </div>
        <!-- 底部 -->
        <div id="com_botton">
            <p>最近30天积分记录</p>
            <div>
                <img src="../imgs/xiaoren.png" alt="">
                <p>最近30天无积分记录</p>
                <p>快去下单赚取大量积分吧</p>
            </div>
        </div>
        <!-- 弹框 -->
      <div id="box" v-if="showtool">
        <img src="../imgs/tishi.jpg" alt>
        <p>快去下单赚取大量积分吧</p>
        <button @click="closeTool()">确认</button>
      </div>
    </div>
</template>
<script>
export default {
    name:"myjifen",
    data() {
        return {
            showtool:false
        }
    },
    methods: {
        backClick(){
            this.$router.back();
        },
        goEx(){
            this.$router.push({
                name:"myjifen_ex"
            })
        },
        closeTool(){
            this.showtool=false
        },
        openTool(){
            this.showtool=true
        }
    },
}
</script>
<style scoped>
    #com_botton p:nth-of-type(2){
        font-size: 0.14rem;
        color: gray;
    }
</style>



