// 子路由代金券部分
<template>
  <div id="bc">
    <!-- 中间路由部分 -->
    <div id="youhui_center">
      <span @click="goHongbao()">红包</span>
      <span @click="goDaijin()">
        商家代金券
        <p></p>
      </span>
    </div>
    <!-- 底部 -->
    <div id="daijin_bottom">
      <p @click="goDaijinEx()">
        <span>？</span>商家代金券说明
      </p>
      <br>
      <img src="../imgs/daijin.jpg" alt>
      <p>无法使用代金券</p>
      <p>非客户端或客户端版本过低</p>
      <button @click="goGown()">下载或升级客户端</button>
    </div>
  </div>
</template>
<script>
export default {
  name: "youhui_daijin",
  methods: {
    goDaijin() {
      this.$router.push({
        name: "youhui_daijin"
      });
    },
    goHongbao() {
      this.$router.push({
        name: "youhui_hongbao"
      });
    },
    goGown() {
      this.$router.push({
        name: "download_eleme"
      });
    },
    goDaijinEx() {
      this.$router.push({
        name: "daijin_ex"
      });
    }
  }
};
</script>

<style scoped>
/* 中间路由部分 */
#youhui_center {
  position: fixed;
  top: 0.42rem;
  left: 0;
  width: 100%;
  padding: 0.1rem 0 0.1rem 0;
  background-color: #fff;
}
#youhui_center span {
  display: inline-block;
  width: 49%;
  text-align: center;
  font-size: 0.18rem;
}
#youhui_center span:nth-child(2) {
  color: #3190e8;
}
#youhui_center > span > p {
  width: 0.8rem;
  height: 0.02rem;
  background-color: blue;
  margin-left: 0.5rem;
}
/* 底部 */
#daijin_bottom {
  text-align: center;
}
#daijin_bottom p:nth-child(1) {
  margin: 0.1rem 0.1rem 0 0;
  float: right;
  color: #3190e8;
}
#daijin_bottom p:nth-child(1) span {
  width: 0.1rem;
  height: 0.1rem;
  background-color: #3190e8;
  border-radius: 50%;
  text-align: center;
  color: white;
  padding-left: 0.05rem;
}
#daijin_bottom img {
  margin-top: 1.7rem;
  margin-left: 1.2rem;
  width: 0.8rem;
}
#daijin_bottom p:nth-of-type(2) {
  font-size: 0.2rem;
  margin-top: 0.2rem;
}
#daijin_bottom p:nth-of-type(3) {
  font-size: 0.14rem;
  color: gray;
}
#daijin_bottom button {
  width: 43%;
  font-size: 0.18rem;
  color: white;
  background-color: #56d176;
  padding: 0.05rem 0.05rem;
  border: 0;
  border-radius: 0.05rem;
}
</style>



