<template>
  <div id="sp_top">
    <div id="sp">
      <!-- 商品展示 -->
      <span @click="goshopgoods()">商品</span>
      <!-- 评价 -->
      <span @click="goshopevaluation()">评价</span>
      <p></p>
    </div>
    <div id="scroll_value">
      <!-- 评价分数 -->
      <div id="valua">
        <div id="valua_left">
          <p>{{Number(valuatedatas.overall_score).toFixed(1)}}</p>
          <p>综合评分</p>
          <p>高于周边商家{{valua1}}%</p>
        </div>
        <div id="valua_right">
          <p>
            <span style="float:left;">服务态度</span>
            <span style="float:left">
              <!-- 评分 -->
              <van-rate
                :size="14"
                v-model="valuatedatas.service_score"
                allow-half
                void-icon="star"
                void-color="#eee"
                readonly
              />
            </span>
          </p>
          <p>
            <span style="float:left;">菜品评价</span>
            <span style="float:left">
              <van-rate
                :size="15"
                v-model="valuatedatas.food_score"
                allow-half
                void-icon="star"
                void-color="#eee"
                readonly
              />
            </span>
          </p>
          <p>
            <span style="float:left;">送达时间</span>
            <span style="float:left">分钟</span>
          </p>
        </div>
      </div>

      <!-- 评价选项内容 -->
      <div class="valua_v">
        <ul>
          <li
            :class="{changecolor:changeBlue == i}"
            @click="choose(i)"
            :key="i"
            v-for="(v,i) in sortdatas"
          >
            <p>
              <span>{{v.name}}</span>
              <span>({{v.count}})</span>
            </p>
          </li>
        </ul>
      </div>

      <!-- 评价内容 -->
      <div id="valua_infor">
        <ul>
          <li :key="i" v-for="(v,i) in infordatas">
            <div id="valua_infor_left">
              <img v-if="v.avatar" :src="'https://fuss10.elemecdn.com/'+v.avatar+'.jpeg'" />
              <img v-else src="../imgs/touxiang-kong.png" alt />
            </div>

            <div id="valua_infor_right">
              <p>
                <span>{{v.username}}</span>
                <span>{{v.rated_at}}</span>
              </p>
              <p>
                <span>
                  <el-rate
                    v-model="v.rating_star"
                    disabled
                    show-score
                    void-color="#f5f5f5"
                    text-color="#ff9900"
                  ></el-rate>
                </span>
                <span>{{v.time_spent_desc}}</span>
              </p>

              <div class="midimg" v-if="v.item_ratings">
                <div class="taotu">
                  <div class="midgp" v-for="(k,b) in v.item_ratings" :key="'a'+b">
                    <img
                      v-if="k.image_hash"
                      :src="'https://fuss10.elemecdn.com/'+k.image_hash+'.jpeg'"
                      class="imggg"
                    />
                    <!-- <span>{{k.food_name}}</span> -->
                  </div>
                </div>
                <div class="baoguo">
                  <div class="midgp2" v-for="(k,b) in v.item_ratings" :key="'a'+b">
                    <span>{{k.food_name}}</span>
                  </div>
                </div>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "shopevaluation",
  data() {
    return {
      // 评价分数
      valuatedatas: "",
      //评价分类
      sortdatas: "",
      //获取评价内容
      infordatas: "",
      valua1: "",
      valua2: 0,
      valua3: 0,
      //设置点击的样式
      changeBlue: ""
    };
  },
  created() {
    this.getevaluate();
    // 19获取评价分类
    this.getsort();
    this.getinfor();
  },

  methods: {
    //   跳转页面
    goshopgoods() {
      this.$router.push({
        name: "shopgoods"
      });
    },
    // 改变点击标签的样式
    choose(index) {
      this.changeBlue = index;
    },
    goshopevaluation() {
      this.$router.push({
        name: "shopevaluation"
      });
    },
    // 获取获取18评价分数
    getevaluate() {
      // 获取vuex中的全局资源
      const api =
        "https://elm.cangdu.org/ugc/v2/restaurants/" +
        this.$store.state.shopinformationId +
        "/ratings/scores";
      this.$http({
        url: api,
        method: "get",
        data: {
          //post的请求参数
        }
      }).then(res => {
        //res是请求返回的数据
        this.valuatedatas = res.data;
        // console.log(this.valuatedatas);
        this.valua1 = Number(100 * this.valuatedatas.compare_rating).toFixed(1);
        this.valua2 = Number(this.valuatedatas.service_score).toFixed(1);
        this.valua3 = Number(this.valuatedatas.food_score).toFixed(1);
      });
    },
    // 19获取评价分类
    getsort() {
      // 获取vuex中的全局资源
      const api =
        "https://elm.cangdu.org/ugc/v2/restaurants/" +
        this.$store.state.shopinformationId +
        "/ratings/tags";
      this.$http({
        url: api,
        method: "get",
        data: {
          //post的请求参数
        }
      }).then(res => {
        //res是请求返回的数据
        this.sortdatas = res.data;
        // console.log(this.sortdatas);
      });
    },
    //17、获取评价信息
    getinfor() {
      // 获取vuex中的全局资源
      const api =
        "https://elm.cangdu.org/ugc/v2/restaurants/" +
        this.$store.state.shopinformationId +
        "/ratings";
      this.$http({
        url: api,
        method: "get",
        data: {
          //post的请求参数
        }
      }).then(res => {
        //res是请求返回的数据
        this.infordatas = res.data;
        // console.log(this.infordatas);
      });
    }
  }
};
</script>
<style scoped>
#sp {
  margin-top: 1rem;
  position: relative;
}
#sp_top {
  height: 100vh;
  overflow-y: scroll;
  overflow-x: hidden;
}

#sp span {
  display: inline-block;
  width: 49%;
  height: 0.4rem;
  line-height: 0.4rem;
  color: black;
  /* border: 1px solid black; */
  box-sizing: border-box;
  text-align: center;
  font-size: 0.2rem;
}
#sp span:nth-child(2) {
  color: blue;
}
#sp p {
  width: 0.6rem;
  height: 0.03rem;
  background-color: blue;
  position: absolute;
  left: 2.5rem;
  top: 0.4rem;
}
#valua {
  border-top: 1px solid gray;
  margin-top: 0.1rem;
  padding: 0.1rem;
}
#valua_left {
  float: left;
  margin-left: 0.2rem;
  text-align: center;
  margin-right: 0.2rem;
  /* border: 1px solid firebrick; */
}

#valua_left p:nth-child(1) {
  font-size: 0.3rem;
  color: red;
}
#valua_left p:nth-child(2) {
  font-size: 0.2rem;
}
#valua_right {
  float: left;
  /* border: 1px solid gold; */
  margin-top: 0.1rem;
}
#valua_right p:nth-child(2) {
  /* border: 1px solid black; */
  margin-bottom: 0.6rem;
  margin-top: 0.3rem;
}
#valua_right p:nth-child(1) span:nth-child(1) {
  font-size: 0.15rem;
  margin-right: 0.1rem;
}
#valua_right p:nth-child(1) span:nth-child(2) {
  margin-top: 0.05rem;
}
#valua_right p:nth-child(2) span:nth-child(2) {
  margin-top: 0.05rem;
}
#valua_right p:nth-child(3) span:nth-child(2) {
  margin-top: 0.05rem;
}
#valua_right p:nth-child(2) span:nth-child(1) {
  font-size: 0.15rem;
  margin-right: 0.1rem;
}
#valua_right p:nth-child(3) span:nth-child(1) {
  font-size: 0.15rem;
  margin-right: 0.1rem;
}
#valua_right p:nth-child(3) span:nth-child(2) {
  font-size: 0.1rem;
}
.valua_v {
  border-bottom: 1px solid gray;
  border-top: 1px solid gray;
  margin-top: 1.1rem;
  padding-top: 0.1rem;
}
.valua_v ul {
  overflow: hidden;
}
.valua_v ul li {
  float: left;
  margin-right: 0.15rem;
  font-size: 0.15rem;
  background-color: burlywood;
  margin-bottom: 0.1rem;
  padding: 0.05rem;
  border-radius: 15%;
}
.changecolor {
  background-color: blue !important;
  color: white;
}

#valua_infor li {
  width: 3.75rem;
  border-bottom: 1px solid gray;
  padding-bottom: 0.1rem;
  overflow: hidden;
}
#valua_infor ul img {
  width: 0.8rem;
}
#valua_infor p:nth-child(1) span:nth-child(1) {
  font-size: 0.18rem;
}
#valua_infor p:nth-child(1) span:nth-child(2) {
  font-size: 0.15rem;
  float: right;
}
#valua_infor p:nth-child(2) {
  overflow: hidden;
}
#valua_infor p:nth-child(2) span:nth-child(1) {
  float: left;
  margin-right: 0.1rem;
}
#valua_infor p:nth-child(2) span:nth-child(2) {
  float: left;
}
#valua_infor_left {
  float: left;
  width: 0.7rem;
  margin-top: 0.3rem;
  margin-left: 0.1rem;
}
#valua_infor_left img {
  height: 0.7rem;
  border-radius: 50%;
}
#valua_infor_right {
  float: left;
  margin-left: 0.3rem;
  margin-top: 0.2rem;
  width: 2.6rem;
}
.taotu {
  overflow: hidden;
}
.midgp {
  float: left;
  width: 0.7rem;
}
.baoguo {
  margin-top: 0.1rem;
  float: left;
}
.midgp2 {
  border: 1px solid gray;
  border-radius: 20%;
  float: left;
  width: 0.5rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.midgp2 span {
  font-size: 0.15rem;
  padding: 0.05rem;
}
</style>



