// 历史红包
<template>
  <div id="bc">
    <!-- 头部 -->
    <div id="com_blue">
      <img @click="goBack()" src="../imgs/back.png" alt>
      <span>历史红包</span>
    </div>
    <!-- 过期红包ul -->
    <div>
      <ul id="hongbao_ul">
        <li v-for="(v,i) in data" :key="i">
          <span>
            ￥
            <span>{{v.amount}}</span>
            <p>{{v.description_map.sum_condition}}</p>
          </span>
          <span>
            <p>{{v.name}}</p>
            <p>{{v.description_map.validity_periods}}</p>
            <p>{{v.description_map.phone}}</p>
          </span>
          <span>
            <img src="../imgs/guoqi.jpg" alt>
          </span>
          <p>{{v.limit_map?v.limit_map.restaurant_flavor_ids:" "}}</p>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import { Loading } from "element-ui";
let loadingInstance;
export default {
  name: "his_hongbao",
  data() {
    return {
      data: [],
      userId: ""
    };
  },
  created() {
    loadingInstance = Loading.service({
      fullscreen: true,
      text: "拼命加载中……"
    });
    this.userId = this.$store.state.userMsg.user_id;
    this.getGuoqi();
  },
  methods: {
    goBack() {
      this.$router.back();
    },
    getGuoqi() {
      if (this.userId) {
        const api =
          "https://elm.cangdu.org/promotion/v2/users/" +
          this.userId +
          "/expired_hongbaos?limit=20&offset=0";
        this.$http({
          url: api,
          method: "get"
        }).then(res => {
          this.data = res.data;
          loadingInstance.close();
        });
      }
    }
  }
};
</script>
<style scoped>
#bc {
  height: 100%;
}
#com_blue {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}
/* 过期红包 */
#hongbao_ul {
  overflow: hidden;
  margin-top: 0.45rem;
}
#hongbao_ul li {
  background-color: #fff;
  border-top: 0.07rem solid #ccc;
  margin-top: 0.1rem;
  width: 90%;
  margin-left: 5%;
  overflow: hidden;
}

#hongbao_ul li > span:nth-of-type(1) {
  border-right: 0.01rem dotted #ddd;
  font-size: 0.25rem;
  color: #ccc;
  padding: 0.1rem 0.15rem 0.1rem 0.05rem;
  float: left;
}
#hongbao_ul li > span:nth-of-type(1) p:nth-of-type(1) {
  font-size: 0.13rem;
  color: #ccc;
}
#hongbao_ul li > span:nth-of-type(2) {
  padding: 0.1rem 0 0.1rem 0.05rem;
  width: 1.65rem;
  float: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
#hongbao_ul li > span:nth-of-type(2) p:nth-of-type(1) {
  font-size: 0.18rem;
}
#hongbao_ul li > span:nth-of-type(2) p:nth-of-type(2),
#hongbao_ul li > span:nth-of-type(2) p:nth-of-type(3) {
  color: #ccc;
  font-size: 0.1rem;
}
#hongbao_ul li > span:nth-of-type(3) {
  float: right;
}
#hongbao_ul li > span:nth-of-type(3) img {
  width: 0.6rem;
  margin-top: 0.1rem;
}
#hongbao_ul li > p {
  width: 100%;
  padding-left: 0.05rem;
  padding-top: 0.02rem;
  float: left;
  font-size: 0.1rem;
  color: #999;
  background-color: #ddd;
}
</style>



