// 推荐有奖
<template>
  <div id="bc">
    <!-- 头部 -->
    <div id="com_blue">
      <img @click="goBack()" src="../imgs/back.png" alt>
      <span>推荐有奖</span>
    </div>
    <!-- 中间邀请图片 -->
    <div id="bigImg">
        <img src="../imgs/yaoqing.jpg" alt="">
    </div>
    <!-- 底部 -->
    <div id="right_bot">
        <div id="bot_top">
            <span><img @click="openTool()" src="../imgs/weixin2.png" alt=""><p>邀请微信好友</p></span>
            <span><img @click="openTool()" src="../imgs/qq.png" alt=""><p>邀请qq好友</p></span>
            <span><img @click="openTool()" src="../imgs/erweima.png" alt=""><p>面对面邀请</p></span>
        </div>
        <div id="bot_bot">
            <div id="bot_bot_top">
                <span>累计收益<p><span>{{menoey}}</span>元</p></span>
                <span>成功邀请<p><span>{{people}}</span>人</p></span>
            </div>
            <div id="bot_bot_bot">
                <p>-收益明细-</p>
                <div>
                    <img src="../imgs/dingdan.png" alt="">
                    <p>还不赶紧去邀请好友</p>
                </div>
            </div>
        </div>
    </div>
    <!-- 弹框 -->
      <div id="box" v-if="showtool">
        <img src="../imgs/tishi.jpg" alt>
        <p>请在饿了么APP中打开</p>
        <button @click="closeTool()">确认</button>
      </div>
  </div>
</template>
<script>
export default {
  name: "hongbao_right",
  data() {
      return {
          menoey:"1",
          people:"1",
          showtool:false
      }
  },
  methods: {
      goBack(){
          this.$router.back();
      },
      closeTool(){
          this.showtool=false;
      },
      openTool(){
          this.showtool=true;
      }
  },
};
</script>

<style scoped>

    /* 中间大图 */
    #bigImg img{
        width: 100%;
    }
    /* 底部 */
    #right_bot {
        margin-top: 0.1rem;
    }
    #bot_top{
         background-color: #fff;
    }
    #bot_top,#bot_bot_top{
        display: flex;
        justify-content: space-around;
       
    }
    #bot_top>span{
        text-align: center;
    }
    #bot_top img{
        width: 0.6rem;
        margin: 0.1rem 0 0.05rem 0;
    }
     #bot_top p{
         font-size: 0.14rem;
         margin-bottom: 0.2rem;
     }
     /* 底部邀请好友部分 */
    #bot_bot{
        margin-top: 0.2rem;
    }
    #bot_bot_top>span{
        width: 50%;
        text-align: center;
        font-size: 0.13rem;
    }
    #bot_bot_top>span:nth-of-type(1){
        border-right: 0.01rem solid gray;
    }
    #bot_bot_top>span p>span{
        font-weight: bold;
        font-size: 0.15rem;
    } 
    #bot_bot_top>span:nth-of-type(1) p>span:nth-of-type(1){
        color: red;
    }

    /* 底部邀请好友收益 */
    #bot_bot_bot{
        text-align: center;
    }
    #bot_bot_bot>p:nth-child(1){
        font-size: 0.14rem;
        margin: 0.1rem 0 0.1rem 0;
    }
    #bot_bot_bot>div>img{
        margin: 0.2rem 0 0.1rem 0;
        width: 0.4rem;
    }
    #bot_bot_bot>div p{
        font-size: 0.14rem;
        color: gray;
    }
</style>



