<template>
  <div>
    <div id="detail">
      <span @click="goback()">
        <img src="../imgs/back.png">
      </span>
      <span>商家详情</span>
    </div>
    <p id="detail_p">活动与属性</p>
    
    <div v-if="taildatas.activities" id="detail_top">
      <p v-if="taildatas.activities">
        <!-- <span v-if="taildatas.activities[0].icon_name">{{taildatas.activities[0].icon_name}}</span>
        <span>{{taildatas.activities[0].description}}(APP专享)</span> -->
      </p>
      <p>
        <span>新</span>
        <span>新用户ji---(APP专享)</span>
      </p>
      <p v-if="taildatas.supports[0]"> 
        <!-- <span v-if="taildatas.supports[0].icon_name">{{taildatas.supports[0].icon_name}}</span>
        <span>{{taildatas.supports[0].description}}(APP专享)</span> -->
      </p>
      <p v-if="taildatas.supports[1]">
        <!-- <span v-if="taildatas.supports[1].icon_name">{{taildatas.supports[1].icon_name}}</span>
        <span>{{taildatas.supports[1].description}}(APP专享)</span> -->
      </p>
    </div>
    <div id="detail_center">
      <p>
        <span>食品监督安全公示</span>
        <span>
          企业认证详情
          <img src="../imgs/right.png" alt>
        </span>
      </p>
    </div>
    <div id="detail_center_top">
      <div>
        <img src="../imgs/icon_xiaolian-mian.png" alt>
      </div>
      <div>
        <p>
          监督检查结果：
          <span>良好</span>
        </p>
        <p>检查日期：</p>
      </div>
    </div>

    <div id="detail_bottom">
      <p style="font-size: 0.15rem;">商家信息</p>
      <p>{{taildatas.name}}</p>
      <p>
        <span>地址：</span>
        <span>{{taildatas.address}}</span>
      </p>
      <p>
        <span>营业时间:</span>
        <span>{{taildatas.opening_hours}}</span>
      </p>
      <p>
        <span>营业执照</span>
        <span style="float:right">
          <img src="../imgs/right.png" alt>
        </span>
      </p>
      <p>
        <span>餐饮服务许可证</span>
        <span style="float:right">
          <img src="../imgs/right.png" alt>
        </span>
      </p>
    </div>
  </div>
</template>
<script>
export default {
  name: "shopdetails",
  data() {
    return {
      taildatas: ""
    };
  },
  created() {
    this.getdetaildatas();
  },
  methods: {
    goback() {
      //返回上一级
      this.$router.go(-1);
    },
    getdetaildatas() {
      // 获取vuex中的全局资源
      const api =
        "https://elm.cangdu.org/shopping/restaurant/" +
        this.$store.state.shopinformationId;
      this.$http({
        url: api,
        method: "get",
        data: {
          //post的请求参数
        }
      }).then(res => {
        //res是请求返回的数据
        this.taildatas = res.data;
        // console.log(this.taildatas);
      });
    }
  }
};
</script>
<style scoped>
#detail {
  background-color: #3190e8;
  color: white;
  height: 0.5rem;
  line-height: 0.5rem;
}
#detail img {
  width: 0.15rem;
  margin-left: 0.1rem;
}
#detail span:nth-child(2) {
  font-size: 0.2rem;
  margin-left: 1rem;
  font-weight: bold;
}
#detail_p {
  font-size: 0.15rem;
  font-weight: bold;
  padding-left: 0.1rem;
  padding-top: 0.1rem;
  padding-bottom: 0.1rem;
  margin-top: 0.05rem;
  border-top: 0.01rem solid #ddd;
  border-bottom: 0.01rem solid #ddd;
}
#detail_top {
  border-bottom: 0.01rem solid #ddd;
  padding-bottom: 0.1rem;
}
#detail_top p {
  margin-top: 0.1rem;
  margin-left: 0.1rem;
}
#detail_top p span {
  margin-right: 0.1rem;
  padding: 0.02rem;
  border-radius: 20%;
}
#detail_top p:nth-child(1) span:nth-child(1) {
  color: white;
  background-color: red;
}
#detail_top p:nth-child(2) span:nth-child(1) {
  color: white;
  background-color: green;
}
#detail_top p:nth-child(3) span:nth-child(1) {
  color: white;
  background-color: gray;
}
#detail_top p:nth-child(4) span:nth-child(1) {
  color: white;
  background-color: blue;
}
#detail_center p {
  padding: 0.1rem;
  border-bottom: 0.01rem solid #ddd;
  border-top: 0.01rem solid #ddd;
}
#detail_center span:nth-child(1) {
  font-size: 0.15rem;
  font-weight: bold;
}
#detail_center span:nth-child(2) {
  float: right;
  margin-right: 0.1rem;
  font-size: 0.12rem;
}
#detail_center_top {
  padding: 0.2rem;
  border-bottom: 0.01rem solid #ddd;
}
#detail_center_top img {
  width: 0.5rem;
}
#detail_center_top div:nth-child(1) {
  float: left;
  margin-right: 0.2rem;
}
#detail_center_top div:nth-child(2) p {
  margin-top: 0.06rem;
}
#detail_center_top span {
  color: green;
}
#detail_bottom p{
    padding: 0.2rem;
    border-bottom: 0.01rem solid #ddd;
    font-size: 0.12rem;
}
</style>



