// 下载饿了么
<template>
    <div id="bc">
        <!-- 头部 -->
        <div id="com_blue">
            <img @click="goBack()" src="../imgs/back.png" alt="">
            <span>下载</span>
        </div>
        <!-- 底部 -->
        <div id="download_bot">
            <img src="../imgs/eliaomo.png" alt="">
            <p>下载饿了么APP</p>
            <button @click="toolOpen">下载</button>
        </div>
        <!-- 弹框 -->
      <div id="box" v-if="showtool">
        <img src="../imgs/tishi.jpg" alt>
        <p>请前往应用商城下载</p>
        <button @click="closeTool()">确认</button>
      </div>
    </div>
</template>
<script>
export default {
    name:"download_eleme",
    data() {
        return {
            showtool:false
        }
    },
    methods: {
        goBack(){
            this.$router.back();
        },
        closeTool(){
            this.showtool=false;
        },
        toolOpen(){
            this.showtool=true;
        }
    },
}
</script>
<style scoped> 
    /* 底部 */
    #download_bot {
        text-align: center;
    }
    #download_bot img{
        width: 1.8rem;
        margin-top: 0.2rem;
    }
    #download_bot p{
        font-size: 0.2rem;
        margin-top: 0.05rem;
    }
    #download_bot button{
        width: 90%;
        text-align: center;
        color: white;
        padding: 0.05rem;
        background-color: #56d176;
        font-size: 0.2rem;
        border: 0;
        border-radius: 0.05rem;
        margin-top: 0.2rem;
    }
</style>



