// 服务中心
<template>
  <div>
    <!-- 头部 -->
    <div id="com_blue">
      <img @click="goBack()" src="../imgs/back.png" alt>
      <span>服务中心</span>
    </div>
    <!-- 中间跳转部分 -->
    <div id="fwCenter">
      <div id="fw_top">
        <span @click="openTool()">
          <img src="../imgs/kefu.png" alt>
          <p>在线客服</p>
        </span>
        <span id="a">
          <a href="tel:15160696181">
            <img src="../imgs/dianhua.png" alt>
            <p>在线客服</p>
          </a>
        </span>
      </div>
      <div id="hot_qus">热门问题</div>
      <div>
        <ul id="ques">
          <li @click="goEx(i)" v-for="(v,i) in data" :key="i">
            {{v}}
            <img src="../imgs/right.png" alt>
          </li>
        </ul>
      </div>
    </div>
    <!-- 弹框 -->
    <div id="box" v-if="showTool">
      <img src="../imgs/tishi.jpg" alt>
      <p>请在APP中打开</p>
      <button @click="closeTool()">确认</button>
    </div>
  </div>
</template>
<script>
export default {
  name: "fwcenter",
  data() {
    return {
      data: [
        "超级会员权益问题",
        "签到规则",
        "用户等级说明",
        "积分问题",
        "教我拍大片",
        "支付问题",
        "其他问题",
        "准时达问题",
        "会员说明",
        "会员问题",
        "红包问题",
        "活动细则",
        "补签规则",
        "优惠说明",
        "免责说明",
        "代金券说明",
        "商务合作",
        "余额问题",
        "超赞商家",
        "匿名购买",
        "活动问题"
      ],
      showTool: false,
      explain: "",
      explain1: ""
    };
  },
  created() {},
  methods: {
    openTool() {
      this.showTool = true;
    },
    closeTool() {
      this.showTool = false;
    },
    goBack() {
      this.$router.back();
    },
    goEx(i) {
      const api = "https://elm.cangdu.org/v3/profile/explain";
      if (i == 0) {
        this.$http({
          url: api,
          method: "get"
        }).then(res => {
          this.$router.push({
            name: "hot_question",
            query: {
              supervipCaption: res.data.supervipCaption,
              supervipContent: res.data.supervipContent
            }
          });
        });
      } else if (i == 1) {
        this.$http({
          url: api,
          method: "get"
        }).then(res => {
          this.$router.push({
            name: "hot_question",
            query: {
              signCaption: res.data.signCaption,
              signContent: res.data.signContent
            }
          });
        });
      } else if (i == 2) {
        this.$http({
          url: api,
          method: "get"
        }).then(res => {
          this.$router.push({
            name: "hot_question",
            query: {
              rankCaption: res.data.rankCaption,
              rankContent: res.data.rankContent
            }
          });
        });
      } else if (i == 3) {
        this.$http({
          url: api,
          method: "get"
        }).then(res => {
          this.$router.push({
            name: "hot_question",
            query: {
              pointtextCaption: res.data.pointtextCaption,
              pointtextContent: res.data.pointtextContent
            }
          });
        });
      } else if (i == 4) {
        this.$http({
          url: api,
          method: "get"
        }).then(res => {
          this.explain = res.data.phototutorialCaption;
          this.explain1 = res.data.phototutorialContent;
          var a = 1;
          var b = false;
          var str = this.explain1;
          var arr = [];
          var reg1 = /\([^\)]+\)/g;
          str.replace(reg1, function(s, value) {
            var s2 = s.substring(1, s.length - 1);
            arr.push(s2); //所有图片的路径
          });
          var str2 = this.explain1;
          var arr2 = [];
          var reg2 = /#{2}[^\<]+(。|~)/g;
          str2.replace(reg2, function(s, value) {
            var s3 = s.substring(2, s.length - 2);
            console.log(s3);
            arr2.push(s3); //所有图片的路径
          });
          this.$router.push({
            name: "hot_question",
            query: {
              data: arr,
              datas: arr2,
              A: a,
              B: b,
              title: this.explain
            }
          });
        });
      } else if (i == 5) {
        this.$http({
          url: api,
          method: "get"
        }).then(res => {
          this.$router.push({
            name: "hot_question",
            query: {
              paymentCaption: res.data.paymentCaption,
              paymentContent: res.data.paymentContent
            }
          });
        });
      } else if (i == 6) {
        this.$http({
          url: api,
          method: "get"
        }).then(res => {
          this.$router.push({
            name: "hot_question",
            query: {
              otherCaption: res.data.otherCaption,
              otherContent: res.data.otherContent
            }
          });
        });
      } else if (i == 7) {
        this.$http({
          url: api,
          method: "get"
        }).then(res => {
          this.$router.push({
            name: "hot_question",
            query: {
              ontimeCaption: res.data.ontimeCaption,
              ontimeContent: res.data.ontimeContent
            }
          });
        });
      } else if (i == 8) {
        this.$http({
          url: api,
          method: "get"
        }).then(res => {
          this.$router.push({
            name: "hot_question",
            query: {
              memberTimesCaption: res.data.memberTimesCaption,
              memberTimesContent: res.data.memberTimesContent
            }
          });
        });
      } else if (i == 9) {
        this.$http({
          url: api,
          method: "get"
        }).then(res => {
          this.$router.push({
            name: "hot_question",
            query: {
              memberCaption: res.data.memberCaption,
              memberContent: res.data.memberContent
            }
          });
        });
      } else if (i == 10) {
        this.$http({
          url: api,
          method: "get"
        }).then(res => {
          this.$router.push({
            name: "hot_question",
            query: {
              hongbaoCaption: res.data.hongbaoCaption,
              hongbaoContent: res.data.hongbaoContent
            }
          });
        });
      } else if (i == 11) {
        this.$http({
          url: api,
          method: "get"
        }).then(res => {
          this.$router.push({
            name: "hot_question",
            query: {
              freshmanCaption: res.data.freshmanCaption,
              freshmanContent: res.data.freshmanContent
            }
          });
        });
      } else if (i == 12) {
        this.$http({
          url: api,
          method: "get"
        }).then(res => {
          this.$router.push({
            name: "hot_question",
            query: {
              forgotCaption: res.data.forgotCaption,
              forgotContent: res.data.forgotContent
            }
          });
        });
      } else if (i == 13) {
        this.$http({
          url: api,
          method: "get"
        }).then(res => {
          this.$router.push({
            name: "hot_question",
            query: {
              discountCaption: res.data.discountCaption,
              discountContent: res.data.discountContent
            }
          });
        });
      } else if (i == 14) {
        this.$http({
          url: api,
          method: "get"
        }).then(res => {
          this.$router.push({
            name: "hot_question",
            query: {
              disclaimerCaption: res.data.disclaimerCaption,
              disclaimerContent: res.data.disclaimerContent
            }
          });
        });
      } else if (i == 15) {
        this.$http({
          url: api,
          method: "get"
        }).then(res => {
          this.$router.push({
            name: "hot_question",
            query: {
              couponCaption: res.data.couponCaption,
              couponContent: res.data.couponContent
            }
          });
        });
      } else if (i == 16) {
        this.$http({
          url: api,
          method: "get"
        }).then(res => {
          this.$router.push({
            name: "hot_question",
            query: {
              businessCaption: res.data.businessCaption,
              businessContent: res.data.businessContent
            }
          });
        });
      } else if (i == 17) {
        this.$http({
          url: api,
          method: "get"
        }).then(res => {
          this.$router.push({
            name: "hot_question",
            query: {
              balanceCaption: res.data.balanceCaption,
              balanceContent: res.data.balanceContent
            }
          });
        });
      } else if (i == 18) {
        this.$http({
          url: api,
          method: "get"
        }).then(res => {
          this.$router.push({
            name: "hot_question",
            query: {
              awesomeRestaurantCaption: res.data.awesomeRestaurantCaption,
              awesomeRestaurantContent: res.data.awesomeRestaurantContent
            }
          });
        });
      } else if (i == 19) {
        this.$http({
          url: api,
          method: "get"
        }).then(res => {
          this.$router.push({
            name: "hot_question",
            query: {
              anonymousBuyCaption: res.data.anonymousBuyCaption,
              anonymousBuyContent: res.data.anonymousBuyContent
            }
          });
        });
      } else if (i == 20) {
        this.$http({
          url: api,
          method: "get"
        }).then(res => {
          this.$router.push({
            name: "hot_question",
            query: {
              activityCaption: res.data.activityCaption,
              activityContent: res.data.activityContent
            }
          });
        });
      }
    }
  }
};
</script>
<style scoped>
#com_blue {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
}
/* 头部 */
#fw_top {
  margin-top: 0.4rem;
  display: flex;
  justify-content: space-around;
}
#fw_top > span {
  width: 50%;
  text-align: center;
  border-bottom: 0.01rem solid #f5f5f5;
}
#fw_top > span:nth-of-type(1) {
  border-right: 0.01rem solid #f5f5f5;
}
#fw_top img {
  padding-top: 0.15rem;
  width: 0.25rem;
}
#a a {
  text-decoration: none;
  color: black;
}
#fw_top > span p {
  margin: 0.05rem 0 0.15rem 0;
  font-size: 0.14rem;
}
/* 热门问题 */
#hot_qus {
  font-size: 0.17rem;
  border-bottom: 0.005rem solid #f5f5f5;
  padding: 0.15rem 0 0.15rem 0.1rem;
}
/* 一大堆问题部分 */
#ques li {
  width: 100%;
  padding: 0.1rem;
  font-size: 0.14rem;
  border-bottom: 0.01rem solid #f5f5f5;
  box-sizing: border-box;
}
#ques li img {
  float: right;
  width: 0.08rem;
  margin-top: 0.03rem;
}
</style>




