<template>
  <div id="shop_t">
    <!-- 商家信息 -->
    <div id="shop">
      <p id="shop_top">
        <span>
          <img src="../imgs/Group-.png" alt />
        </span>
        <span>附近商家</span>
      </p>
      <ul id="shop_mes">
        <li
          @click="getid(v.id)"
          :key="i"
          v-for="(v,i) in datas"
          style="padding: 0.1rem 0.05rem 0.2rem 0.05rem; border-bottom: 1px solid #ddd;"
        >
          <div>
            <img :src="'https://elm.cangdu.org/img/'+v.image_path" />
          </div>
          <div>
            <p style="margin-bottom: 0.05rem;">
              <span style="margin-left: 0.05rem;background-color: gold;">品牌</span>
              <span style="font-size: 0.15rem;">{{v.name}}</span>
              <span
                v-if="v.supports[2]"
                style="float: right;border: 1px solid #ddd;margin-left: 0.03rem;border-radius: 10%;"
              >{{v.supports[2].icon_name}}</span>
              <span
                v-if="v.supports[1]"
                style="float: right;border: 1px solid #ddd;margin-left: 0.03rem;border-radius: 10%;"
              >{{v.supports[1].icon_name}}</span>
              <span
                v-if="v.supports[0]"
                style="float: right;border: 1px solid #ddd;border-radius: 10;"
              >{{v.supports[0].icon_name}}</span>
            </p>
            <p style="margin-bottom: 0.05rem;">
              <span style="margin-left: 0.05rem;">
                <!-- 评分 -->
                <van-rate
                  :size="12"
                  v-model="v.rating"
                  allow-half
                  void-icon="star"
                  void-color="#eee"
                  readonly
                />
                {{v.rating}}
              </span>
              <span style="font-size: 0.1rem;">月售{{v.recent_order_num}}单</span>
              <span
                v-if="v.supports[1]"
                style="border: 0.05px solid #3190e8;float: right;color:#3190e8;"
              >{{v.supports[1].name}}</span>
              <span
                v-if="v.delivery_mode"
                style="background-color: #3190e8;color:white;float: right;"
              >{{v.delivery_mode.text}}</span>
            </p>
            <p style="margin-bottom: 0.05rem;">
              <span style="float: left;">￥{{v.float_minimum_order_amount}}起始</span>
              <span style="margin-right: 0.1rem;float: left;">/{{v.piecewise_agent_fee.tips}}</span>

              <span style="float: right;color:#3190e8;">{{v.order_lead_time}}</span>
              <span style="float: right;">{{v.distance}}/</span>
            </p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import { Loading } from "element-ui";
let loadingInstance;
export default {
  name: "fujsj", //附近商家
  data() {
    return {
      datas: [],
      h: "",
      num1: 10,
      num2: 0, //记录滚动的次数
      namestring: "" //存储分类的店铺类型
    };
  },
  //挂在完成后触发 调用滚动方法
  mounted() {
    //可以在这里面直接进行滚动条的获取
    window.addEventListener("scroll", this.handleScroll, true);
  },
  //挂在前监测手机屏幕高度
  beforeMount() {
    this.h =
      document.documentElement.clientHeight || document.body.clientHeight;
    // console.log(this.h);
    return this.h;
  },
  created() {
    loadingInstance = Loading.service({
      fullscreen: true,
      text: "拼命加载中……"
    });
    this.getdatas();
    this.cityTitle = this.$route.query.cityTitle;
  },
  methods: {
    jiazai() {
      this.num2 += 1;
      this.num1 += 10;
      this.getdatas();
    },

    // 获取滚动高度方法
    handleScroll() {
      // 方法一
      // var top = Math.floor(
      //   document.body.scrollTop ||
      //     document.documentElement.scrollTop ||
      //     window.pageXOffset
      // );
      // // console.log(top);
      // if (top > 260 + 900 * this.num2) {
      //   this.jiazai();
      // }

      const shop = document.getElementById("shop");
      if (shop.scrollTop + shop.clientHeight + 100 >= shop.scrollHeight) {
        this.jiazai();
      }
    },

    // 获得排序数据
    getdatas() {
      // 获取vuex中的全局资源
      const api =
        "https://elm.cangdu.org/shopping/restaurants?latitude=" +
        this.$store.state.latitude +
        "&longitude=" +
        this.$store.state.longitude +
        "&order_by=" +
        this.$store.state.shopID +
        "&limit=" +
        this.num1;
      // console.log(this.$store.state.latitude);
      // console.log(this.$store.state.longitude);
      // console.log(this.$store.state.shopID);
      this.$http({
        url: api,
        method: "get",
        data: {
          //post的请求参数
        }
      }).then(res => {
        //res是请求返回的数据
        this.datas = res.data;
        // console.log(res.data);
        loadingInstance.close();
      });
    },

    // 获得分类数据
    getfenleidatas() {
      // 获取vuex中的全局资源
      const api =
        "https://elm.cangdu.org/shopping/restaurants?latitude=" +
        this.$store.state.latitude +
        "&longitude=" +
        this.$store.state.longitude;

      // 存储商品分类标志
      this.namestring =
        this.$store.state.fendata1 + "/" + this.$store.state.fendata2;
      // console.log(this.namestring);

      this.$http({
        url: api,
        method: "get",
        data: {
          //post的请求参数
        }
      }).then(res => {
        //res是请求返回的数据
        this.datas = []; //先清空数组，不然值会累加
        if (res.data) {
          for (let i = 0; i < res.data.length; i++) {
            if (res.data[i].category == this.namestring) {
              // console.log("aaa");
              this.datas.push(res.data[i]);
              // console.log(this.datas);
            }
          }
        }
      });
    },
    // 获取商品ID为商家展示和商家信息做准备，存储全局vuex中
    getid(id) {
      // console.log(v);
      this.$store.commit("getshopinformID", id);
      this.$router.push({
        name: "shopshow"
      });
    }
  }
};
</script>

<style scoped>
#shop {
  /* margin-top: 0.1rem; */
  /* border-top: 0.01rem solid #ddd; */
  /* border: 1px solid gold; */
  background-color: #f5f5f5;
}
#shop_top img {
  width: 0.3rem;
}
#shop_top span:nth-child(1) {
  margin-left: 0.1rem;
}
#shop_top span:nth-child(2) {
  margin-left: 0.1rem;
  font-size: 0.15rem;
}
/* 商品信息样式设置 */
#shop_mes div:nth-child(1) {
  float: left;
  /* border: 1px solid gold; */
}
#shop_mes div:nth-child(1) img {
  width: 0.6rem;
}
</style>



