<template>
  <div>
    <ul id="goshopche">
      <p id="goshopche_top">
        <span @click="goback()" class="el-icon-arrow-left"></span>
        <span>购物车</span>
        <span @click="clear()">
          <i class="el-icon-delete">清空</i>
        </span>
      </p>
      <li :key="i" v-for="(v,i) in shoparr">
        <p v-if="v.index" id="goshopche_center">
          <span>{{v.name}}</span>
          <span>￥{{v.index*v.price}}</span>
          <span @click="reduce(i)">-</span>
          <span>{{v.index}}</span>
          <span @click="add(i)">+</span>
        </p>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "gowuche",
  data() {
    return {
      shoparr: []
    };
  },
  created() {
    // if (this.$store.state.arrA=="") {
      // this.shoparr = '';
    // }
    this.shoparr = this.$store.state.arrA;
    // console.log(this.shoparr);
  },

  methods: {
    clear() {
      this.shoparr = "";
      this.$store.commit("getfoodinf", {
        id: -1,
        price: -1,
        name: -1,
        index: -1
      });
    },
    goback() {
      this.$router.go(-1);
    },
    reduce(i) {
      this.shoparr[i].index--;
      if (this.shoparr[i].index == 0) {
        this.shoparr[i].index = 0;
      }
      //   此处调用只是为了刷新vuex中给的数据
      this.$store.commit("getfoodinf", {
        id: this.shoparr[i].id,
        price: this.shoparr[i].price,
        name: this.shoparr[i].name,
        index: this.shoparr[i].index
      });
    },
    add(i) {
      this.shoparr[i].index++;
      //   此处调用只是为了刷新vuex中给的数据
      this.$store.commit("getfoodinf", {
        id: this.shoparr[i].id,
        price: this.shoparr[i].price,
        name: this.shoparr[i].name,
        index: this.shoparr[i].index
      });
    }
  }
};
</script>
<style scoped>
#goshopche_top {
  background-color: gainsboro;
  font-size: 0.18rem;
  padding: 0.05rem;
}
.el-icon-arrow-left {
  margin-right: 0.1rem;
}
#goshopche_top span:nth-child(3) {
  float: right;
  margin-right: 0.1rem;
}
#goshopche_center {
  border-bottom: 1px solid gray;
  height: 0.3rem;
  font-size: 0.15rem;
  padding: 0.05rem;
  line-height: 0.3rem;
}
#goshopche_center span:nth-child(1) {
  display: inline-block;
  width: 1.5rem;
  margin-left: 0.1rem;
}
#goshopche_center span:nth-child(2) {
  display: inline-block;
  width: 0.7rem;
  margin-left: 0.1rem;
  color: red;
  font-weight: bold;
  /* border: 1px solid gold; */
}
#goshopche_center span:nth-child(3) {
  display: inline-block;
  border: 1px solid blue;
  text-align: center;
  border-radius: 50%;
  width: 0.2rem;
  height: 0.2rem;
  line-height: 0.2rem;
  margin-left: 0.1rem;
  color: blue;
  font-size: 0.2rem;
  font-weight: bold;
}
#goshopche_center span:nth-child(5) {
  display: inline-block;
  border: 1px solid blue;
  text-align: center;
  border-radius: 50%;
  width: 0.2rem;
  height: 0.2rem;
  line-height: 0.2rem;
  margin-left: 0.1rem;
  color: white;
  background-color: blue;
  font-size: 0.2rem;
  font-weight: bold;
}
</style>




