<template>
  <div>
    <div id="sp">
      <!-- 商品展示 -->
      <span @click.stop="goshopgoods()">商品</span>
      <!-- 评价 -->
      <span @click.stop="goshopevaluation()">评价</span>
      <p></p>
    </div>
    <div id="goods">
      <span id="scrollA" :style="{height:h-200+'px'}">
        <div id="goods_left">
          <p :key="i" v-for="(v,i) in datas">
            <a :href="'#ab'+i">{{v.name}}</a>
          </p>
        </div>
      </span>
      <span id="scrollB" :style="{height:h-200+'px'}">
        <div class="goods_right" :id="'ab'+ids" :key="ids" v-for="(value,ids) in datas">
          <p sty id="goods_right_top">
            <span>{{value.name}}</span>
            <span>{{value.description}}</span>
            <span>...</span>
          </p>
          <div id="goodsA" :key="i" v-for="(v,i) in value.foods">
            <div id="goods_right_left">
              <img :src="basePath+v.image_path" />
            </div>
            <div id="goods_right_right">
              <p>{{v.name}}</p>
              <p>{{v.description}}</p>
              <p>
                <!-- split()字符串分割，返回值是一个数组 -->
                <span>{{(v.tips).split(" ")[1]}}</span>
                <span>好评率{{v.satisfy_rate}}%</span>
              </p>
              <p></p>
              <div id="xgg">
                <span id="xgg_1">￥{{v.specfoods[0].price}}</span>
                <span id="xgg_2">起</span>
                <!-- 购物车小球 -->
                <div class="balls"></div>
                <div id="span3">
                  <span
                    id="span3_1"
                    v-if="v.__v"
                    @click="sub(v.specfoods[0].food_id,v.specfoods[0].price,v.specfoods[0].name,v.__v-1),v.__v--
                    "
                  >-</span>
                  <span id="span3_2" v-if="v.__v">{{v.__v}}</span>
                  <span
                    id="span3_3"
                    class="ballSp"
                    @click="add(v.specfoods[0].food_id,v.specfoods[0].price,v.specfoods[0].name,v.__v+1,i,$event),v.__v++
                    "
                  >+</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </span>
    </div>

    <div id="gowuche">
      <div id="gwc">
        <div @click="gogwc()" :class="$store.state.gshopNum ?  styB : styA">
          <p id="gwc_left_1">
            <img src="../imgs/gouwuche.png" alt />
          </p>
          <p v-if="$store.state.gshopNum" id="gwc_left_2">{{$store.state.gshopNum}}</p>
        </div>
        <div id="gwc_right">
          <div id="gwc_right_left">
            <span id="allprice">￥{{$store.state.gshopAllPr}}</span>
            <p id="const">配送费￥5</p>
          </div>
        </div>
        <p id="gwc_right_right" v-if="$store.state.gshopAllPr-20<0">还差￥{{Math.abs(totalPrice-20)}}起送</p>
        <p id="gwc_right_right2" v-else>去结算</p>
      </div>
    </div>
  </div>
</template>
<script>
import { Loading } from "element-ui";
let loadingInstance;
export default {
  name: "shopgoods",
  data() {
    return {
      datas: "",
      h: "",
      num: 0,
      styA: "gwc_leftA",
      styB: "gwc_leftB",
      basePath: "https://elm.cangdu.org/img/"
    };
  },
  created() {
    loadingInstance = Loading.service({
      fullscreen: true,
      text: "拼命加载中……"
    });
    this.getdatas();
  },
  beforeMount() {
    //挂在前监测手机屏幕高度
    this.h =
      document.documentElement.clientHeight || document.body.clientHeight;
    // console.log(this.h);
    return this.h;
  },
  methods: {
    // 切换到shopgods商品界面
    goshopgoods() {
      this.$router.push({
        name: "shopgoods"
      });
    },
    goshopevaluation() {
      this.$router.push({
        name: "shopevaluation"
      });
    },
    //跳转到购物车页面
    gogwc() {
      this.$router.push({
        name: "gowuche"
      });
    },
    //  商品数量增加
    add(v1, v2, v3, v4, i, evt) {
      this.$store.commit("getfoodinf", {
        id: v1,
        price: v2,
        name: v3,
        index: v4
      });
      // console.log(this);

      const ballSpObj = document.getElementsByClassName("ballSp");
      const $ball = document.getElementsByClassName("balls");
      // for (let i = 0; i < ballSpObj.length; i++) {
      // ballSpObj[i].onclick = function(evt){
      // console.log(i);
      window.event ? (window.event.cancelBubble = true) : e.stopPropagation();
      $ball[i].style.top = evt.pageY + "px";
      $ball[i].style.left = evt.pageX + "px";
      $ball[i].style.transition = "left 0s, top 0s";
      setTimeout(() => {
        $ball[i].style.top = window.innerHeight + "px";
        $ball[i].style.left = "0px";
        $ball[i].style.transition = "left 1s linear, top 1s ease-in";
      }, 20);
      // }
      // }
    },
    //  商品数量减少
    sub(v1, v2, v3, v4) {
      this.$store.commit("getfoodinf", {
        id: v1,
        price: v2,
        name: v3,
        index: v4
      });
    },
    // 获取商家信息
    getdatas() {
      // 获取vuex中的全局资源
      const api =
        "https://elm.cangdu.org/shopping/v2/menu?restaurant_id=" +
        this.$store.state.shopinformationId;
      this.$http({
        url: api,
        method: "get",
        data: {
          //post的请求参数
        }
      }).then(res => {
        //res是请求返回的数据
        this.datas = res.data;
        // console.log(res.data);
        loadingInstance.close();
      });
    }
  },
  computed: {
    //计算属性
    // 合计总价
    totalPrice: function() {
      var total = 0; //存储商品的价格
      for (var i = 0; i < this.datas.length; i++) {
        for (let j = 0; j < this.datas[i].foods.length; j++) {
          total +=
            this.datas[i].foods[j].specfoods[0].price *
            this.datas[i].foods[j].__v;
        }
      }
      return total.toFixed(2);
    },
    // 数量
    totalNum: function() {
      var totalNum = 0; //存储商品的价格
      for (var i = 0; i < this.datas.length; i++) {
        for (let j = 0; j < this.datas[i].foods.length; j++) {
          totalNum += this.datas[i].foods[j].__v;
        }
      }
      // console.log("qqqq");
      // console.log(totalNum);
      return totalNum;
    }
  }
};
</script>
<style scoped>
.balls {
  width: 0.2rem;
  height: 0.2rem;
  border-radius: 50%;
  background-color: blue;
  position: absolute;
  left: -10px;
  top: 0;
  transition: left 1s linear, top 1s ease-in;
  z-index: 0;
}
#scrollA {
  overflow-y: scroll;
  overflow-x: hidden;
  height: 4rem;
  display: inline-block;
}
#scrollB {
  overflow-y: scroll;
  overflow-x: hidden;
  display: inline-block;
  width: 2.6rem;
  /* margin-left: 0.1rem; */
  height: 4rem;
  /* border: 1px solid gold; */
}
#goodsA {
  border-bottom: 1px solid gray;
  margin-bottom: 1rem;
  /* height: 1.1rem; */
  margin-left: 0.1rem;
}
#sp {
  margin-top: 1rem;
  padding-bottom: 0.1rem;
  border-bottom: 1px solid gray;
}
#sp span {
  display: inline-block;
  width: 49%;
  height: 0.4rem;
  line-height: 0.4rem;
  color: black;
  /* border: 1px solid black; */
  box-sizing: border-box;
  text-align: center;
  font-size: 0.2rem;
}
#sp span:nth-child(1) {
  color: blue;
}
#sp p {
  width: 0.6rem;
  height: 0.03rem;
  background-color: blue;
  margin-left: 0.6rem;
}
#goods {
  box-sizing: border-box;
}
#goods_left {
  float: left;
  width: 1rem;
  height: 0.4rem;
  text-align: center;
  line-height: 0.4rem;
  font-size: 0.18rem;
  margin-right: 0.05rem;
  background-color: #f5f5f5;
}
#goods_left a {
  color: black;
  /* font-weight: bold; */
  text-decoration: none;
}
#goods_left p {
  background-color: #f5f5f5;
  border-bottom: 1px solid gray;
}
#goods_left p:nth-child(1) {
  background-color: white;
}
.goods_right {
  /* border: 1px solid firebrick; */
  float: right;
  width: 2.7rem;
  border-bottom: 1px solid gray;
}
#goods_right_top {
  background-color: #f5f5f5;
  padding: 0.05rem;
  padding-left: 0.2rem;
  /* border: 1px solid gold; */
}
#goods_right_top span:nth-child(1) {
  font-size: 0.2rem;
  font-weight: bold;
  margin-right: 0.1rem;
}
#goods_right_top span:nth-child(3) {
  font-size: 0.2rem;
  float: right;
  margin-right: 0.1rem;
}
/* 照片部分 */
#goods_right_left {
  float: left;
  margin-left: 0.05rem;
  margin-right: 0.05rem;
}
#goods_right_left img {
  width: 0.6rem;
  margin-top: 0.2rem;
}
#goods_right_right {
  float: left;
  width: 1.9rem;
  /* border: 1px solid rebeccapurple; */
}
#goods_right_right p:nth-child(1) {
  font-size: 0.18rem;
  font-weight: bold;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
#goods_right_right p:nth-child(2) {
  font-size: 0.12rem;
}
#goods_right_right p:nth-child(3) span:nth-child(1) {
  font-size: 0.13rem;
}
#goods_right_right p:nth-child(3) span:nth-child(2) {
  font-size: 0.13rem;
  margin-left: 0.1rem;
}
#goods_right_right p:nth-child(4) {
  /* font-size: 0.12rem; */
  width: 0.17rem;
  text-align: center;
  border-radius: 50%;
  color: red;
  /* border: 1px solid red; */
}
#xgg_1 {
  float: left;
  font-size: 0.15rem;
  color: red;
}
#xgg_2 {
  float: left;
  font-size: 0.15rem;
  margin-left: 0.1rem;
  /* border: 1px solid green; */
  /* margin-right: 0.7rem; */
}
#span3 {
  /* width: 1.4rem; */
  /* border: 1px solid gold; */
  margin-top: 0.03rem;
  float: right;
  z-index: 11;
}
#span3_1 {
  display: inline-block;
  color: blue;
  height: 0.2rem;
  width: 0.2rem;
  border: 1px solid blue;
  font-size: 0.2rem;
  text-align: center;
  line-height: 0.2rem;
  border-radius: 50%;
  font-weight: bold;
  /* padding-left: 0.02rem; */
}
#span3_2 {
  border: 1px solid gold;
  display: inline-block;
  height: 0.2rem;
  /* width: 0.2rem; */
  font-size: 0.2rem;
  text-align: center;
  line-height: 0.2rem;
  text-align: center;
  margin-right: 0.05rem;
}
#span3_3 {
  display: inline-block;
  color: white;
  height: 0.2rem;
  width: 0.2rem;
  border: 1px solid blue;
  background-color: blue;
  font-size: 0.2rem;
  text-align: center;
  line-height: 0.2rem;
  border-radius: 50%;
  font-weight: bold;
}
#gowuche {
  position: fixed;
  left: 0;
  /* top: 6.2rem; */
  top: 93vh;
  width: 3.75rem;
  height: 0.5rem;
  background-color: black;
}
#gwc {
  position: relative;
}
.gwc_leftA {
  position: fixed;
  left: 0.2rem;
  top: 90vh;
  width: 0.6rem;
  height: 0.6rem;
  border-radius: 50%;
  background-color: black;
}
.gwc_leftB {
  position: fixed;
  left: 0.2rem;
  top: 90vh;
  width: 0.6rem;
  height: 0.6rem;
  border-radius: 50%;
  background-color: blue;
}
#gwc_left_1 {
  position: absolute;
  top: 0.12rem;
  left: 0.08rem;
}
#gwc_left_2 {
  position: absolute;
  left: 0.44rem;
  width: 0.2rem;
  height: 0.2rem;
  padding: 0.02rem;
  background-color: red;
  color: white;
  border-radius: 50%;
  line-height: 0.2rem;
  text-align: center;
}
#gowuche img {
  width: 0.4rem;
}
#gwc_left p:nth-child(2) {
  font-size: 0.15rem;
  color: white;
}
#gwc_right {
  position: absolute;
  left: 1rem;
  color: white;
}
#gwc_right_left {
  float: left;
}
#allprice {
  font-size: 0.2rem;
  color: white;
  margin-left: 0.1rem;
}
#const {
  margin-left: 0.15rem;
  color: white;
}
#gwc_right_right {
  float: right;
  color: white;
  background-color: gray;
  height: 0.5rem;
  line-height: 0.5rem;
  font-size: 0.15rem;
  padding-left: 0.1rem;
  padding-right: 0.1rem;
  width: 1rem;
  text-align: center;
}
#gwc_right_right2 {
  text-align: center;
  width: 1rem;
  float: right;
  color: white;
  background-color: green;
  height: 0.5rem;
  line-height: 0.5rem;
  font-size: 0.2rem;
  padding-left: 0.1rem;
  padding-right: 0.1rem;
}
</style>



