// 地址部分
<template>
  <div id="bc">
    <!-- 头部 -->
    <div id="add_top">
      <img @click="goBack()" src="../imgs/back.png" alt>
      <span>编辑地址</span>
      <span @click="showimg()">{{text}}</span>
    </div>
    <!-- 加入的地址 -->
    <div>
      <ul id="addAdd">
        <li v-for="(v,i) in addressData" :key="i">
          <p>{{v.address_detail}}</p>
          <p>{{v.phone}}</p>
          <img @click="deleAdd(i)" v-if="showImg" src="../imgs/quxiao.png" alt>
        </li>
      </ul>
    </div>
    <!-- 添加地址按钮 -->
    <div id="addAddress" @click="goNewAddress()">
      <span>新增地址</span>
      <img src="../imgs/iconfontjiantou2.png" alt>
    </div>
  </div>
</template>
<script>
import { Loading } from "element-ui";
let loadingInstance;
export default {
  name: "Aaddress",
  data() {
    return {
      addressData: [],
      text: "编辑",
      showImg: false
    };
  },
  created() {
    loadingInstance = Loading.service({
      fullscreen: true,
      text: "拼命加载中……"
    });
    this.getAddress();
  },
  methods: {
    goBack() {
      this.$router.push({
        name: "fourth_info"
      });
    },
    goNewAddress() {
      this.$router.push({
        name: "newAddress"
      });
    },
    getAddress() {
      const api =
        "https://elm.cangdu.org/v1/users/" +
        this.$store.state.userMsg.user_id +
        "/addresses";
      this.$http({
        url: api,
        method: "get"
      }).then(res => {
        this.addressData = res.data;
        this.addressData.reverse();
        loadingInstance.close();
      });
    },
    showimg() {
      if (this.text == "完成") {
        this.text = "编辑";
        this.showImg = false;
      } else {
        this.text = "完成";
        this.showImg = true;
      }
    },
    deleAdd(i) {
      const api =
        "https://elm.cangdu.org/v1/users/" +
        this.$store.state.userMsg.user_id +
        "/addresses/" +
        this.addressData[i].id;
      this.$http({
        url: api,
        method: "DELETE"
      }).then(res => {
        this.addressData.splice(i, 1);
      });
    }
  }
};
</script>
<style scoped>
/* 头部 */
#add_top {
  width: 100%;
  background-color: #3190e8;
  padding: 0 0 0.09rem 0.1rem;
  position: fixed;
  top: 0;
  left: 0;
}
#add_top img {
  width: 0.15rem;
}
#add_top span:nth-of-type(1) {
  font-size: 0.2rem;
  margin-left: 1.2rem;
  font-weight: bold;
  color: white;
}
#add_top span:nth-of-type(2) {
  float: right;
  font-size: 0.15rem;
  color: white;
  margin: 0.05rem 0.3rem 0 0;
}
/* 地址 */
#addAdd {
  margin-top: 0.7rem;
  border-top: 0.01rem solid #ddd;
}
#addAdd li {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  border-bottom: 0.01rem solid #ddd;
  padding-left: 0.1rem;
  background-color: #fff;
  float: left;
}
#addAdd li:nth-of-type(1) {
  background-color: #fff8c3;
}
#addAdd li p {
  font-size: 0.15rem;
  margin-top: 0.05rem;
}
#addAdd li p:nth-of-type(1) {
  padding-top: 0.05rem;
}
#addAdd li p:nth-of-type(2) {
  padding-bottom: 0.05rem;
}

#addAdd img {
  position: absolute;
  top: 0.25rem;
  right: 0.1rem;
}

#addAddress {
  box-sizing: border-box;
  width: 100%;
  float: left;
  margin-top: 0.1rem;
  border-top: 0.01rem solid #ddd;
  border-bottom: 0.01rem solid #ddd;
  background-color: white;
  padding: 0.1rem 0 0.1rem 0.1rem;
}
#addAddress span {
  font-size: 0.15rem;
}
#addAddress img {
  width: 0.2rem;
  float: right;
  margin-right: 0.2rem;
}
</style>



