<template>
  <div id="serch_se">
    <div id="scroll_ser">
      <div id="search_top">
        <p>
          <span>
            <router-link to="home">
              <img src="../imgs/back.png" alt />
            </router-link>
          </span>
          <span>{{name}}</span>
          <span>
            <router-link to="home">切换城市</router-link>
          </span>
        </p>
      </div>
      <div id="search">
        <input type="text" v-model="se" placeholder="输入学校,商务楼,地址" />
        <button @click="getsearch()">提交</button>
        <!-- 切换到历史页面 -->
        <ul id="ser">
          <li :key="i" v-for="(v,i) in datas" @click="pushAll(i)">
            <!-- 将数据发送到first页面 -->
            <p>{{v.name}}</p>
            <p>{{v.address}}</p>
          </li>
        </ul>
        <div v-if="show1" id="history1">
          <p>
            <!-- <router-link to="/second">second页面</router-link> -->
            <router-link to="/history">搜索历史</router-link>
            <router-view></router-view>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Loading } from "element-ui";
let loadingInstance;
export default {
  name: "search",
  data() {
    return {
      datas: [],
      show1: true,
      id: "",
      name: "",
      se: "" //搜索内容
    };
  },
  created() {
    // 接收编程式路由传递过来的值
    // console.log(this.$route);
    // this.datas = this.$route.query;
    // 接收常规路由传递过来的值
    this.id = this.$store.state.cityId;
    this.name = this.$store.state.cityName;
    // this.id = this.$route.query.cityId;
    // this.name = this.$route.query.cityName;
  },
  methods: {
    // 传值
    pushAll(i) {
      this.$store.commit("getshopName", {
        a: this.datas[i].latitude,
        b: this.datas[i].longitude,
        c: this.datas[i].name,
        d: this.datas[i].address
      });
      this.$router.push({
        name: "first"
      });
    },

    getsearch() {
      loadingInstance = Loading.service({
        fullscreen: true,
        text: "拼命加载中……"
      });
      this.show1 = false;
      const api =
        "https://elm.cangdu.org/v1/pois?city_id=" +
        this.id +
        "&keyword=" +
        this.se;
      this.$http({
        url: api,
        method: "get",
        data: {}
      }).then(res => {
        //编程式路由跳转，传值query或者params传值
        this.datas = res.data;
        // console.log(this.datas);
        loadingInstance.close();
      });
    }
  }
};
</script>

<style scoped>
#serch_se {
  /* overflow: hidden; */
  height: 100vh;
}
#scroll_ser{
  height: 100vh;
  overflow-y: scroll;
  overflow-x: hidden;
}
#search_top {
  width: 100vw;
  height: 0.4rem;
  background-color: #3190e8;
  line-height: 0.4rem;
  font-size: 0.17rem;
}
#search_top span a {
  color: white;
}
#search_top span:nth-child(1) {
  float: left;
  font-size: 0.25rem;
  margin-left: 0.1rem;
}
#search_top span:nth-child(1) img {
  width: 0.13rem;
}
#search_top span:nth-child(2) {
  float: left;
  margin-left: 1.3rem;
  color: white;
}
#search_top span:nth-child(3) {
  float: right;
  margin-right: 0.1rem;
}
#search > input {
  width: 90%;
  font-size: 0.18rem;
  margin-left: 5%;
  margin-top: 0.2rem;
  border: 1px solid #ddd;
  border-radius: 0.01rem;
}
#search > button {
  width: 90%;
  background-color: #3190e8;
  color: white;
  height: 0.4rem;
  font-size: 0.2rem;
  margin-left: 5%;
  margin-top: 0.1rem;
  border: 0;
  border-radius: 0.05rem;
}
#history1 p {
  margin-top: 0.1rem;
  /* padding-left: 0.1rem; */
  width: 100vw;
  height: 0.3rem;
  line-height: 0.25rem;
  font-size: 0.18rem;
  background-color: gainsboro;
}
#ser li {
  margin-top: 0.1rem;
  margin-bottom: 0.1rem;
  padding: 0.05rem;
  border-top: 0.01rem solid #ddd;
}
#ser li p:nth-child(1) {
  font-size: 0.18rem;
  margin-left: 0.1rem;
  margin-bottom: 0.05rem;
}
#ser li p:nth-child(2) {
  font-size: 0.12rem;
  margin-left: 0.1rem;
  color: #aaa;
}
</style>




