// 饿了么会员卡
<template>
  <div id="bc">
    <!-- 头部 -->
    <div id="com_blue">
      <img @click="backClick()" src="../imgs/back.png" alt>
      <span>会员中心</span>
    </div>
    <!-- 中间 -->
    <div id="vip_center">
      <p>为账户  {{$store.state.userMsg.username||" "}}   购买会员</p>
      <div id="vip_ex">
        <span>会员特权</span>
        <span @click="goVipEx()">
          会员说明
          <img src="../imgs/right.png" alt>
        </span>
        <div class="vip_ex">
          <span><img src="../imgs/yewutubiaosheng.png" alt></span>
          <span>
            <p>减免配送费</p>
            <p>每月减免30单，每日可减免3单，每日最高减4元蜂鸟转送专享</p>
          </span>
        </div>
        <div class="vip_ex">
          <span><img src="../imgs/zhun1.png" alt></span>
          <span>
            <p>减免配送费</p>
            <p>每月减免30单，每日可减免3单，每日最高减4元蜂鸟转送专享</p>
          </span>
        </div>
      </div>
      <!-- 底部 -->
      <div id="vip_bot">
          <div id="vip_bot_top">
              <p>开通会员</p>
              <p><span>1个月<span>￥20</span></span><button @click="goBuyVip()">购买</button></p>
          </div>
          <div class="vip_bot_cen">
              <span>兑换会员</span>
              <span @click="goExChangeVip()">使用卡号卡密<img src="../imgs/right.png" alt=""></span>
          </div>
          <div class="vip_bot_cen">
              <span>购买记录</span>
              <span @click="goVipFapiao()">开发票<img src="../imgs/right.png" alt=""></span>
          </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "eleme_vip",
  methods: {
    backClick() {
      this.$router.back();
    },
    goVipEx(){
        this.$router.push({
            name:"vip_ex"
        })
    },
    goExChangeVip(){
        this.$router.push({
            name:"exchange_vip"
        })
    },
    goVipFapiao(){
        this.$router.push({
            name:"vip_fapiao"
        })
    },
    goBuyVip(){
        this.$router.push({
            name:"buy_vip"
        })
    }
  }
};
</script>
<style scoped>
/* 中间 */
#vip_center>P{
    font-size: 0.14rem;
    padding: 0.1rem;
}
#vip_ex{
    background-color: #fff;
    padding-top: 0.1rem;
}
#vip_ex>span:nth-of-type(1){
    font-size: 0.16rem;
    margin-left: 0.1rem;
}
#vip_ex>span:nth-of-type(2){
    font-size: 0.15rem;
    float: right;
    margin-right: 0.1rem;
}
#vip_ex>span:nth-of-type(2) img{
    width: 0.08rem;
    margin-bottom: 0.03rem;
}
/* 中间图片部分 */
.vip_ex{
    overflow: hidden;
    margin-top: 0.1rem;
    padding: 0.18rem 0.3rem 0.1rem 0.1rem;
    border-top: 0.01rem solid #ddd; 
}
.vip_ex img{
    width: 0.6rem;
}
.vip_ex>span:nth-of-type(2){
    float: right;
    width: 78%;
}
.vip_ex>span:nth-of-type(2) p:nth-of-type(1){
    font-size: 0.17rem;
}
.vip_ex>span:nth-of-type(2) p:nth-of-type(2){
    font-size: 0.13rem;
    color: gray;
}
/* 底部三部分 */
/* 第一部分 */
#vip_bot_top{
    background-color: #fff;
    margin-top: 0.1rem;
    padding: 0.1rem;
    font-size: 0.15rem
}
#vip_bot_top p:nth-of-type(1){
    border-bottom:0.01rem solid #ddd;
    padding-bottom: 0.1rem; 
    margin-bottom: 0.1rem;
}
#vip_bot_top p:nth-of-type(2)>span>span{
   color: rgb(255, 94, 0);
}
#vip_bot_top p:nth-of-type(2) button{
    float: right;
    width: 0.6rem;
    padding: 0.02rem;
    text-align: center;
    border:0.01rem solid orange;
    background-color: #fff;
    font-size: 0.15rem;
    color: orange;
    border-radius: 0.05rem;
}
/* 第二三部分 */
.vip_bot_cen{
    background-color: #fff;
    margin-top: 0.1rem;
    padding: 0.1rem;
    font-size: 0.15rem;
}
.vip_bot_cen span:nth-of-type(2){
    float: right;
    color: gray;
}
.vip_bot_cen span:nth-of-type(2) img{
    width: 0.08rem;
    margin-left: 0.05rem;
}
</style>



