// 登录成功之后的用户信息
<template>
  <div id="bc">
    <!-- 头部 -->
    <div id="com_blue">
      <img @click="goBack()" src="../imgs/back.png" alt>
      <span>账户信息</span>
    </div>
    <!-- 中间第一部分 -->
    <div>
      <ul id="info_center_f">
        <li>
          <span>头像</span>
          <span>
            <el-upload
              class="avatar-uploader"
              action="https://jsonplaceholder.typicode.com/posts/"
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload"
            >
              <img id="img1" :src="imageUrl" class="avatar">
              <i class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </span>
          <img src="../imgs/iconfontjiantou2.png" alt>
        </li>
        <li @click="goChangeUser()">
          <span>用户名</span>
          <span>{{name}}</span>
          <img src="../imgs/iconfontjiantou2.png" alt>
        </li>
        <li @click="goAdd()">
          <span>收货地址</span>
          <img src="../imgs/iconfontjiantou2.png" alt>
        </li>
      </ul>
      <ul id="info_center_s">
        <li>
          <p>账号绑定</p>
          <p @click="tooltip()">
            <img src="../imgs/dianhua.png" alt>
            <span>手机</span>
            <img src="../imgs/iconfontjiantou2.png" alt>
          </p>
        </li>
        <li>
          <p>安全设置</p>
          <p @click="goChangePassword()">
            <span>登录密码</span>
            <span>
              修改
              <img src="../imgs/iconfontjiantou2.png" alt>
            </span>
          </p>
        </li>
      </ul>
    </div>
    <!-- 退出登录 -->
    <button @click="outLogin()" id="but">退出登录</button>
    <div id="box" v-if="show">
      <img src="../imgs/tishi.jpg" alt>
      <p>请在手机APP中设置</p>
      <button @click="closeTool()">确认</button>
    </div>
    <!-- 弹框 -->
    <div id="outbox" v-if="outShow">
      <img src="../imgs/tishi.jpg" alt>
      <p>是否退出登录</p>
      <button id="outbut" @click="closeTool2()">再等等</button>
      <button id="outbut2" @click="outLoginTool()">退出登录</button>
    </div>
  </div>
</template>
<script>
export default {
  name: "fourth_info",
  data() {
    return {
      info_user: "",
      show: false,
      outShow: false,
      imageUrl: "",
      name: ""
    };
  },
  created() {
    this.name = this.$store.state.userMsg.username;
    this.info_user = this.$route.query.nowusername;
    this.imageUrl = this.$store.state.imgs;
    if(this.getImg()){
      this.getImg();
    }
  },
  methods: {
    getImg(){
      const api="https://elm.cangdu.org/v1/user/"+this.$store.state.userMsg.user_id;
      this.$http({
        url:api,
        method:"get"
      }).then(res=>{
        this.imageUrl="https://elm.cangdu.org/img/"+res.data.avatar;
      })
    },
    goBack() {
      this.$router.push({
        name: "fourth"
      });
    },
    goChangePassword() {
      this.$router.push({
        name: "change_pass"
      });
    },
    goChangeUser() {
      this.$router.push({
        name: "change_user"
      });
    },
    goAdd() {
      this.$router.push({
        name: "Aaddress"
      });
    },
    outLogin() {
      const api = "https://elm.cangdu.org/v2/signout";
      this.$http({
        url: api,
        method: "get"
      }).then(res => {
        this.outShow = true;
      });
    },
    tooltip() {
      this.show = true;
    },
    closeTool() {
      this.show = false;
    },
    closeTool2() {
      this.outShow = false;
    },
    outLoginTool() {
      this.$store.state.userMsg = "";
      this.$store.state.imgs = "";
      this.$router.push({
        name: "fourth"
      });
    },
    beforeAvatarUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return  isLt2M;
    },
    handleAvatarSuccess(res, file) {
      //立刻显示
      this.imageUrl = URL.createObjectURL(file.raw);
      var file = file.raw;
      var formdata1 = new FormData(); // 创建form对象
      formdata1.append("file", file, file.name); // 通过append向form对象添加数据,可以通过append继续添加数据或formdata1.append('img',file);
      let config = {
        headers: { "Content-Type": "multipart/form-data" }
      }; //添加请求头
      this.axios.post(`https://elm.cangdu.org/eus/v1/users/${this.$store.state.userMsg.user_id}/avatar`, formdata1, config).then(response => {
        //这里的/xapi/upimage为接口
      });
    }
  }
};
</script>

<style scoped>
#img1{
  border-radius: 50%;
}
/* 中间第一部分 */
#info_center_f {
  margin-top: 0.1rem;
  border-top: 0.01rem solid #ddd;
}
#info_center_f li > span:nth-of-type(1) {
  font-size: 0.15rem;
}
#info_center_f li {
  border-bottom: 0.01rem solid #ddd;
  background-color: #fff;
  padding: 0.1rem;
}
#info_center_f li > img {
  width: 0.15rem;
  float: right;
}
#info_center_f li:nth-of-type(1) {
  padding: 0.25rem 0.1rem 0.25rem 0.1rem;
}
#info_center_f li > span:nth-of-type(2) img {
  width: 0.5rem;
  position: absolute;
  right: 0rem;
  top: -0.15rem;
}
#info_center_f li :nth-of-type(2) {
  position: relative;
}
#info_center_f li > span:nth-of-type(2) {
  position: absolute;
  right: 0.3rem;
  font-size: 0.15rem;
}
/* 中间第二部分 */
#info_center_s li p:nth-of-type(1) {
  padding: 0.1rem;
  font-size: 0.1rem;
  color: gray;
}
#info_center_s li p:nth-of-type(2) img {
  width: 0.2rem;
}
#info_center_s li p:nth-of-type(2) {
  border-top: 0.01rem solid #ddd;
  border-bottom: 0.01rem solid #ddd;
  padding: 0.1rem;
  font-size: 0.15rem;
  background-color: #fff;
}
#info_center_s li p:nth-of-type(2) img:nth-of-type(2),
#info_center_s li p:nth-of-type(2) span:nth-of-type(2) {
  float: right;
}
#info_center_s li p:nth-of-type(2) span:nth-of-type(2) {
  font-size: 0.15rem;
}
/* 退出登录 */
#but {
  width: 94%;
  margin: 0.3rem 0 2% 2%;
  background-color: #d8584a;
  font-size: 0.15rem;
  border: 0;
  color: white;
  padding: 0.15rem 0.15rem;
  border-radius: 0.05rem;
}
/* 退出登录提示框 */
#outbox {
  width: 3.6rem;
  background-color: #f6f6f6;
  text-align: center;
  position: absolute;
  top: 1.5rem;
  left: 0.1rem;
  border-radius: 0.1rem;
  overflow: hidden;
  animation: heartBeat 1s;
  box-sizing: border-box;
}
#outbox img {
  width: 1.4rem;
  margin-top: 0.3rem;
  margin-bottom: 0.1rem;
}
#outbox p {
  font-size: 0.25rem;
  margin-bottom: 0.4rem;
}
#outbut,
#outbut2 {
  padding: 0.1rem;
  border: 0;
  color: white;
  border-radius: 0.05rem;
  margin-bottom: 0.1rem;
  font-size: 0.15rem;
}
#outbut {
  width: 0.9rem;
  margin-right: 0.2rem;
  background-color: #ddd;
}
#outbut2 {
  width: 1.2rem;
  background-color: #d8584a;
}
</style>



