<template>
  <div id="first">
    <div id="first_top">
      <span>
        <!-- 搜索链接到搜索页面 -->
        <router-link to="/second">
          <img src="../imgs/fangdajing.png" alt>
        </router-link>
      </span>
      <span id="name1" @click="backHome()">{{name}}</span>
      <span>
        <!-- 搜索链接到我的页面 -->
        <router-link to="/fourth">
          <img src="../imgs/touxiang2.png" alt>
        </router-link>
      </span>
    </div>
    <!-- 轮播图部分 -->
    <div id="lunbo" v-if="show">
      <swiper :options="swiperOption" ref="mySwiper">
        <!-- slides -->
        <swiper-slide>
          <div class="sw1">
            <div :key="i" v-for="(v,i) in data1">
              <!-- <router-link :to="'/first?cityId=' + v.latitude + '&cityName='+v.name + '&cityaddress='+v.longitude"> -->
              <router-link :to="'/Foods?cityTitle=' + v.title">
                <img :src="'https://fuss10.elemecdn.com'+v.image_url">
                <p>{{v.title}}</p>
              </router-link>
            </div>
          </div>
        </swiper-slide>

        <swiper-slide>
          <div class="sw1">
            <div :key="i" v-for="(v,i) in data2">
              <router-link :to="'/Foods?cityTitle=' + v.title">
                <img :src="'https://fuss10.elemecdn.com'+v.image_url">
                <p>{{v.title}}</p>
              </router-link>
            </div>
          </div>
        </swiper-slide>
        <!-- Optional controls -->
        <div class="swiper-pagination" slot="pagination"></div>
        <div class="swiper-scrollbar" slot="scrollbar"></div>
      </swiper>
    </div>

    <!-- 底部导航信息 -->
    <div id="change_botton">
      <router-link to="/first">
        <div>
          <img src="../imgs/SSS1.png" alt>
          <p>外卖</p>
        </div>
      </router-link>
      <router-link to="/second">
        <div>
          <img src="../imgs/zhinanzhen.png" alt>
          <p>搜索</p>
        </div>
      </router-link>
      <router-link to="/third">
        <div>
          <img src="../imgs/icon-.png" alt>
          <p>订单</p>
        </div>
      </router-link>
      <router-link to="/fourth">
        <div>
          <img src="../imgs/touxiang2.png" alt>
          <p>我的</p>
        </div>
      </router-link>
    </div>
    <!-- 附近商家出口 -->
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: "first",
  data() {
    return {
      id: "",
      show: false,
      name: "",
      address: "",
      serdatas: "",
      datas: [], //存储食品分类数据
      data1: [], //存储食品分类数据
      data2: [], //存储食品分类数据
      // 轮播图
      swiperOption: {
        // some swiper options/callbacks
        // 所有的参数同 swiper 官方 api 参数   swiper4.x
        pagination: {
          el: ".swiper-pagination"
        },
        loop: true,
        effect: "slide"
      },
    };
  },

  created() {
    // 接收编程式路由传递过来的值
    // console.log(this.$route);
    // this.datas = this.$route.query;
    
    // 接收来自设serch页面传递过来的数据
    //存储vuex全局的经度 全局资源
    this.$store.commit("getLatitude", this.$route.query.cityId);
    //存储vuex全局的纬度 全局资源
    this.$store.commit("getLongitude", this.$route.query.cityaddress);

    this.$store.commit("getLongitude", this.$route.query.cityaddress);
   
   // 存储本地地址localStorage
    this.name = this.$store.state.cityname; //name
    this.address = this.$store.state.cityadrs; //name

    if (localStorage.name1) {
      localStorage.name1 += "," + this.name;
      localStorage.address1 += "," + this.address;
    } else {
      localStorage.name1 = this.name;
      localStorage.address1 = this.address;
    }
    // 获取轮播图的数据
    this.getdatas();
    // this.getserdatas();
  },
  methods: {
    backHome(){
      this.$router.push({
        name:"home"
      })
    },
    getdatas() {
      const api = "https://elm.cangdu.org/v2/index_entry";
      this.$http({
        url: api,
        method: "get",
        data: {
          //post的请求参数
        }
      }).then(res => {
        //res是请求返回的数据
        this.show = true;
        this.datas = res.data;
        if (this.datas) {
           for (let index = 0; index < this.datas.length; index++) {
               if (index <= 7) {
                  this.data1.push(this.datas[index]);
            } else {
                  this.data2.push(this.datas[index]);
               }
            }
        }
       
      });
    }
  }
};
</script>
<style scoped>
#name1{
  color: white;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  float: left;
  margin-left: 10%;
  width: 60%;
}
#first {
  width: 100%;
  height: 100vh;
  background-color: #ddd;
}
#first_top {
  background: #3190e8;
  width: 100%;
  height: 0.4rem;
  text-align: center;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 2;
}
#first_top span a img {
  width: 0.3rem;
  float: left;
}

#first_top span:nth-child(1) {
  float: left;
  margin-left: 0.1rem;
  margin-top: 0.07rem;
}
#first_top span:nth-child(2) {
  line-height: 0.4rem;
  font-size: 0.2rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  /* border: 1px solid greenyellow; */
}
#first_top span:nth-child(3) {
  float: right;
  margin-top: 0.05rem;
  margin-right: 0.1rem;
}
#lunbo {
  margin-top: 0.4rem;
  background-color: #f5f5f5;
}
.sw1 {
  /* border: 0.01rem solid gray; */
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  /* border-bottom: 1px solid gray; */
  padding-bottom: 0.2rem;
}
.sw1 div {
  width: 25%;
  /* border: 1px solid gold; */
  box-sizing: border-box;
  text-align: center;
}
.sw1 img {
  text-align: center;
  width: 0.7rem;
}
</style>




