// 搜索地址
<template>
  <div>
    <!-- 头部 -->
    <div id="com_blue">
      <img @click="goBack()" src="../imgs/back.png" alt>
      <span>搜索地址</span>
    </div>
    <!-- 搜索框 -->
    <div id="search">
      <input
        type="text"
        placeholder="请输入小区/写字楼/学校等"
        v-model="search"
        @input="watchInp()"
        ref="type"
      >
      <button @click="getSearch()">确认</button>
      <p>为了满足商家送餐要求，建议您从列表中选择地址</p>

      <ul id="se_ul" v-if="showJieguo">
        <li v-for="(v,i) in searchAll" :key="i" @click="jumpBack(i)">
          {{v.name}}
          <p>{{v.address}}</p>
        </li>
      </ul>

      <div id="search_text" v-if="showTishi">
        <p>找不到地址</p>
        <p>请尝试输入小区、写字楼或学校名</p>
        <p>详细地址(如门牌号)可稍后输入哦</p>
      </div>
      <ul>
        <li></li>
      </ul>
    </div>
  </div>
</template>
<script>
import { Loading } from "element-ui";
let loadingInstance;
export default {
  nameL: "newAddress",
  data() {
    return {
      showDite: [],
      searchAll: [],
      nowCity: "",
      search: "",
      showTishi: true,
      showJieguo: false
    };
  },
  created() {
    this.id = this.$route.query.cityId;
    this.nowCity = this.$route.query.cityName;
    this.search = this.searchAll.name;
  },
  methods: {
    goBack() {
      this.$router.back();
    },
    getSearch() {
      loadingInstance = Loading.service({
        fullscreen: true,
        text: "拼命加载中……"
      });
      const api =
        "https://elm.cangdu.org/v1/pois?city_id=" +
        this.id +
        "&keyword=" +
        this.search;
      this.$http({
        url: api,
        method: "get"
      }).then(res => {
        this.searchAll = res.data;
        this.showSite = this.search;
        loadingInstance.close();
      });
      this.showJieguo = true;
      this.showTishi = false;
    },
    watchInp() {
      if (this.$refs.type.value == "") {
        this.showJieguo = false;
        this.showTishi = true;
      }
    },
    jumpBack(i) {
      this.search = this.searchAll[i].name;
      this.$store.commit("addressSite", this.searchAll[i].name);
      this.$router.push({
        name: "newAddress"
      });
    }
  }
};
</script>
<style scoped>
/* 搜索框 */
#search {
  margin-top: 0.1rem;
}
#search input {
  width: 72%;
  margin: 0 0 0.1rem 4%;
  padding: 0.05rem 0.01rem 0.05rem 0.08rem;
  background-color: #ddd;
  border: 0;
  border-radius: 0.05rem;
  font-size: 0.15rem;
}
#search button {
  float: right;
  margin-right: 0.15rem;
  width: 15%;
  text-align: center;
  background-color: rgb(0, 132, 255);
  color: white;
  font-size: 0.15rem;
  border: 0;
  padding: 0.05rem 0.1rem 0.05rem 0.1rem;
  border-radius: 0.05rem;
}
#search > p {
  width: 100%;
  text-align: center;
  font-size: 0.15rem;
  background-color: #fff6e4;
  padding: 0.02rem 0 0.02rem 0;
  color: #ff883f;
}
#search_text {
  text-align: center;
  margin-top: 1.2rem;
  font-size: 0.15rem;
  color: gray;
}
#search_text p {
  margin-top: 0.05rem;
}
/* 搜索出来的结果 */
#se_ul li {
  padding-left: 0.2rem;
  border-bottom: 0.01rem solid #ddd;
  padding-top: 0.08rem;
  font-size: 0.15rem;
  color: gray;
  padding-bottom: 0.05rem;
}
</style>



