// 我的余额
<template>
  <div id="bc">
    <!-- 头部 -->
    <div id="com_top">
      <img @click="backClick()" src="../imgs/back.png" alt>
      <span>我的余额</span>
      <div id="now">
        <span>当前余额</span>
        <div>
          <span>？</span>
          <span @click="goEx()">余额说明</span>
        </div>
        <p>0.00元</p>
        <button id="but">提现</button>
      </div>
    </div>
    <!-- 底部 -->
    <div id="com_botton">
      <p>交易明细</p>
      <div>
        <img src="../imgs/xiaoren.png" alt>
        <p>暂无明细记录</p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "myyue",
  methods: {
    backClick() {
      this.$router.back();
    },
    goEx() {
      this.$router.push({
        name: "myyue_ex"
      });
    }
  }
};
</script>
<style scoped>
#but {
  background-color: #ccc;
}
</style>



