// 城市列表
<template>
  <div>
    <!-- 主页头部 -->
    <div id="home_top">
      <span>ele.me</span>
      <span v-if="showLogin" @click=" login()">登录|注册</span>
      <img @click=" login()" src="../imgs/touxiang2.png" alt="" v-if="showImg">
    </div>
    <!-- 定位城市 -->
    <div id="At_city">
      <div id="At_city_top">
        <span>当前定位城市:</span>
        <span>定位不准时,请在城市列表中选择</span>
      </div>
        <p @click="sendNowCity()">{{atCity}}</p>
      <span @click="sendNowCity()"><img src="../imgs/right.png" alt=""></span>
    </div>
    <!-- 热门城市 -->
    <div id="hot_city">
      <p>热门城市</p>
      <ul id="hot_city_ul">
        <li v-for="(v,i) in gethot" :key="i" @click="sendHotCity(v.id,v.name)">{{v.name}}</li>
      </ul>
    </div>
    <!-- 所有城市 -->
    <div id="all_city">
      <ul id="allCities">
        <li :key="key" v-for="(value,key) in groupCities">
          <h3>
            {{key}}
            <span v-if="key=='A'">(按字母排序)</span>
          </h3>
          <ul>
            <li :key="i" v-for="(v,i) in value" @click="sendAllCity(v.id,v.name)">{{v.name}}</li>
          </ul>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import { Loading } from 'element-ui';
let loadingInstance;
export default {
  name: "home",
  data() {
    return {
      atCity: "",
      gethot: [],
      groupCities: {},
      showLogin:true,
      showImg:false
    };
  },
  created() {
    loadingInstance=Loading.service({
      fullscreen:true,
      text:"拼命加载中……"
    });
    if(this.$store.state.userMsg){
      this.showImg=true;
      this.showLogin=false;
    }else{
      this.showLogin=true;
      this.showImg=false;
    }
    this.getGuess();
    this.getHot();
    this.getGroupCities();
    this.getgeohash();
    
  },
  methods: {
    // 登录
    login(){
      if(this.$store.state.userMsg){
        this.$router.push({
          name:"fourth"
        })
      }else{
        this.$router.push({
          name:"login"
        })
      }
    },
    // 获取定位
    getgeohash(){
      const api = "https://elm.cangdu.org/v2/pois/:geohash";
      this.$http({
        url: api,
        method: "get"
      }).then(res => {
      });
    },
    //   定位城市
    getGuess() {
      const api = "https://elm.cangdu.org/v1/cities?type=guess";
      this.$http({
        url: api,
        method: "get"
      }).then(res => {
        this.atCity = res.data.name;
      });
    },
    // 热门城市
    getHot() {
      const api = "https://elm.cangdu.org/v1/cities?type=hot";
      this.$http({
        url: api,
        method: "get"
      }).then(res => {
        this.gethot = res.data;
      });
    },
    // 所有城市
    getGroupCities() {
      const api = "https://elm.cangdu.org/v1/cities?type=group";
      this.$http({
        url: api,
        method: "get"
      }).then(res => {
         // this.$store.commit("cityData", res.data);
        // this.groupCities=res.data;
        var arr = [];
        //先排序,再给数据源赋值
        for (const key in res.data) {
          arr.push(key);
        }
        arr.sort();
        for (var i = 0; i < arr.length; i++) {
          // this.groupCities[arr[i]] = res.data[arr[i]];
          this.$set(this.groupCities, arr[i], res.data[arr[i]]);
        }
        // this.groupCities = res.data;
        // var arr = [];
        loadingInstance.close();
      });
    },
    // 传城市当前城市
    sendNowCity(){
      this.$store.commit("searchId",{x:this.atCity.id,y:this.atCity})
      this.$router.push({
        name:"search"
      })
    },
    // 传热门城市
    sendHotCity(a,b){
      this.$store.commit("searchId",{x:a,y:b})
      this.$router.push({
        name:"search"
      })
    },
    // 传所有城市
    sendAllCity(a,b){
      this.$store.commit("searchId",{x:a,y:b})
       this.$router.push({
        name:"search"
      })
    }
  }
};
</script>
<style scoped>
/* 主页头部 */
#home_top {
  width: 100%;
  height: 0.4rem;
  background-color: #3190e8;
  color: white;
  position: fixed;
  margin-bottom: 0.1rem;
}
#home_top span {
  margin-left: 0.1rem;
  font-size: 0.2rem;
}
#home_top img{
  float: right;
  width: 0.3rem;
  margin: 0.05rem 0.1rem 0 0;
}
#home_top span:nth-of-type(2) {
  margin-right: 0.1rem;
  color: white;
  float: right;
  font-size: 0.2rem;
}
/* 定位城市 */
#At_city {
  padding-top: 0.45rem;
  overflow: hidden;
  border-bottom: 0.02rem solid #ddd;
  margin-bottom: 0.1rem;
}
#At_city_top {
  padding-bottom: 0.05rem;
  margin-bottom: 0.05rem;
  border-bottom: 0.01rem solid #ddd;
}
#At_city_top span {
  color: gray;
  font-size: 0.13rem;
}
#At_city_top span:nth-of-type(1) {
  margin-left: 0.1rem;
}
#At_city_top span:nth-of-type(2) {
  float: right;
  margin-right: 0.1rem;
}
#At_city > span {
  float: right;
  font-size: 0.2rem;
  margin-bottom: 0.05rem;
  margin-right: 0.1rem;
}
#At_city p {
  float: left;
  margin-left: 0.1rem;
  color: #3190e8;
  font-size: 0.2rem;
}
/* 热门城市 */
#hot_city {
  border-top: 0.02rem solid #ddd;
}
#hot_city p {
  margin-left: 0.1rem;
  margin-top: 0.05rem;
  margin-bottom: 0.05rem;
  font-size: 0.1rem;
}
#hot_city_ul {
  width: 100%;
  /* border: 0.01rem solid gray; */
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 0.1rem;
}
#hot_city_ul>li {
  padding: 0.07rem 0.07rem;
  text-align: center;
  width: 25%;
  display: inline-block;
  border: 0.01rem solid #ddd;

  box-sizing: border-box;
  font-size: 0.14rem;
  /* flex-direction: row; */
  color: #3190e8;
}
/* 所有城市 */
#allCities h3 {
  border-top: 0.02rem solid #ddd;
  padding-bottom: 0.05rem;
  padding-top: 0.05rem;
  padding-left: 0.1rem;
  font-size: 0.1rem;
}
#allCities ul {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 0.1rem;
  border-bottom: 0.01rem solid #ddd;
}
#allCities ul li {
  padding: 0.07rem 0.07rem;
  text-align: center;
  width: 25%;
  display: inline-block;
  border: 0.01rem solid #ddd;
  color: black;
  box-sizing: border-box;
  font-size: 0.14rem;
/* 超出部分省略号显示 */
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>



