<template>
  <div>
    <ul id="history2">
      <li :key="i" v-for="(v,i) in nameArr">
        <router-link :to="'/first?cityId=' + v.latitude + '&cityaddress='+v.longitude + '&cityName='+v.name + '&cityadrs='+v.address">  
          <p>{{v}}</p>
          <p>{{adderssArr[i]}}</p>
        </router-link>
      </li>
    </ul>
    <p v-if="nameArr.length>0" id="clear" @click="clear()">清空所有</p>
  </div>
</template>

<script>
export default {
  name: "history",
  data() {
    return {
      nameArr: [], //存储本地的地址名称
      adderssArr: [] //存储本地的地址地址
    };
  },
  created() {
    this.getdata();
  },
  methods: {
    getdata() {
      // 获取本地localStorage中的值
      const arrA = localStorage.name1;
      const arrB = localStorage.address1;

      this.nameArr = arrA.split(",");
      this.adderssArr = arrB.split(",");
    },
    clear() {
      localStorage.removeItem("name1");
      localStorage.removeItem("address1");
      this.nameArr=[];
      this.adderssArr=[];
      // for (let index = 0; index < arrA.length; index++) {
      //   arrA.pop();
      //   arrB.pop();
      // }
    }
  }
};
</script>
<style scoped>
#history2 li {
  margin-top: 0.1rem;
  margin-bottom: 0.1rem;
  border-bottom: 1px solid gray;
  padding: 0.05rem;
}
#history2 li a {
  color: gray;
}
#history2 li p:nth-child(1) {
  font-size: 0.18rem;
  /* margin-left: 0.1rem; */
  margin-bottom: 0.05rem;
}
#history2 li p:nth-child(2) {
  font-size: 0.12rem;
  /* margin-left: 0.1rem; */
}
#clear{
  margin-top: 0.2rem;
  text-align: center;
  font-size: 0.2rem;
  background-color: #ddd;
  padding-top: 0.08rem;
  padding-bottom: 0.08rem;
  /* font-weight: bold; */
  /* border: 1px solid green; */
}
</style>




