import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/lks/home'
import login from '@/components/lks/login'
import change_pass from '@/components/lks/change_pass'
import first from '@/components/wwl/first'
import second from '@/components/lks/second'
import third from '@/components/lks/third'
import fourth from '@/components/lks/fourth'
import myyue from '@/components/lks/myyue'
import myyue_ex from '@/components/lks/myyue_ex'
import fourth_info from '@/components/lks/fourth_info'
import change_user from '@/components/lks/change_user'
import Aaddress from '@/components/lks/Aaddress'
import myyouhui from '@/components/lks/myyouhui'
import youhui_daijin from '@/components/lks/youhui_daijin'
import youhui_hongbao from '@/components/lks/youhui_hongbao'
import download_eleme from '@/components/lks/download_eleme'
import hongbao_ex from '@/components/lks/hongbao_ex'
import daijin_ex from '@/components/lks/daijin_ex'
import his_hongbao from '@/components/lks/his_hongbao'
import hongbao_left from '@/components/lks/hongbao_left'
import hongbao_right from '@/components/lks/hongbao_right'
import jifen from '@/components/lks/jifen'
import myjifen from '@/components/lks/myjifen'
import myjifen_ex from '@/components/lks/myjifen_ex'
import fwcenter from '@/components/lks/fwcenter'
import hot_question from '@/components/lks/hot_question'
import eleme_vip from '@/components/lks/eleme_vip'
import vip_ex from '@/components/lks/vip_ex'
import exchange_vip from '@/components/lks/exchange_vip'
import vip_fapiao from '@/components/lks/vip_fapiao'
import buy_vip from '@/components/lks/buy_vip'
import newAddress from '@/components/lks/newAddress'
import searchAddress from '@/components/lks/searchAddress'

// wwl
import search from '@/components/wwl/search'
import history from '@/components/wwl/history'
import Foods from '@/components/wwl/Foods'
import fujsj from '@/components/wwl/fujsj'
import shopshow from '@/components/wwl/shopshow'
import paixu from '@/components/wwl/paixu'
import shopgoods from '@/components/wwl/shopgoods'
import shopevaluation from '@/components/wwl/shopevaluation'
import guige from '@/components/wwl/guige'
import shopdetails from '@/components/wwl/shopdetails'
import gowuche from '@/components/wwl/gowuche'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/change_pass',
      name: 'change_pass',
      component: change_pass
    },
    {
      path: '/second',
      name: 'second',
      component: second
    },
    {
      path: '/third',
      name: 'third',
      component: third
    },
    {
      path: '/fourth',
      name: 'fourth',
      component: fourth
    },
    {
      path: '/myyue',
      name: 'myyue',
      component: myyue
    },
    {
      path: '/myyue_ex',
      name: 'myyue_ex',
      component: myyue_ex
    },
    {
      path: '/fourth_info',
      name: 'fourth_info',
      component: fourth_info
    },
    {
      path: '/change_user',
      name: 'change_user',
      component: change_user
    },
    {
      path: '/Aaddress',
      name: 'Aaddress',
      component: Aaddress
    },
    {
      path: '/myyouhui',
      name: 'myyouhui',
      component: myyouhui,
      redirect: '/youhui_hongbao',
      children: [
        {
          path: '/youhui_daijin',
          name: 'youhui_daijin',
          component: youhui_daijin
        },
        {
          path: '/youhui_hongbao',
          name: 'youhui_hongbao',
          component: youhui_hongbao

        }
      ]
    },
    {
      path: '/download_eleme',
      name: 'download_eleme',
      component: download_eleme
    },
    {
      path: '/hongbao_ex',
      name: 'hongbao_ex',
      component: hongbao_ex
    },
    {
      path: '/daijin_ex',
      name: 'daijin_ex',
      component: daijin_ex
    },
    {
      path: '/his_hongbao',
      name: 'his_hongbao',
      component: his_hongbao
    },
    {
      path: '/hongbao_left',
      name: 'hongbao_left',
      component: hongbao_left
    },
    {
      path: '/hongbao_right',
      name: 'hongbao_right',
      component: hongbao_right
    },
    {
      path: '/jifen',
      name: 'jifen',
      component: jifen
    },
    {
      path: '/myjifen',
      name: 'myjifen',
      component: myjifen
    },
    {
      path: '/myjifen_ex',
      name: 'myjifen_ex',
      component: myjifen_ex
    },
    {
      path: '/fwcenter',
      name: 'fwcenter',
      component: fwcenter
    },
    {
      path: '/hot_question',
      name: 'hot_question',
      component: hot_question
    },
    {
      path: '/eleme_vip',
      name: 'eleme_vip',
      component: eleme_vip
    },
    {
      path: '/vip_ex',
      name: 'vip_ex',
      component: vip_ex
    },
    {
      path: '/exchange_vip',
      name: 'exchange_vip',
      component: exchange_vip
    },
    {
      path: '/vip_fapiao',
      name: 'vip_fapiao',
      component: vip_fapiao
    },
    {
      path: '/buy_vip',
      name: 'buy_vip',
      component: buy_vip
    },
    {
      path: '/newAddress',
      name: 'newAddress',
      component: newAddress
    },
    {
      path: '/searchAddress',
      name: 'searchAddress',
      component: searchAddress
    },

    // wwl
    {
      path: '/search',
      name: 'search',
      component: search,
      redirect: '/history',
      //历史记录显示
      children: [
        {
          path: '/history',
          name: 'history',
          component: history
        }
      ],
    },
    {
      path: '/first',
      name: 'first',
      component: first,
      redirect: '/fujsj',
      children: [
        {
          path: '/fujsj',
          name: 'fujsj',
          component: fujsj
        }
      ],
    },
    {
      path: '/Foods',
      name: 'Foods',
      component: Foods,
      //一个组件在俩个页面显示的方式
      redirect: '/fujsj1',
      children: [
        {
          path: '/fujsj1',
          name: 'fujsj1',
          component: fujsj
        }
      ],

    },
    {
      path: '/shopshow',
      name: 'shopshow',
      component: shopshow,
      redirect: '/shopgoods',
      children: [
        {
          path: '/shopgoods',
          name: 'shopgoods',
          component: shopgoods
        },
        {
          path: '/shopevaluation',
          name: 'shopevaluation',
          component: shopevaluation
        },
      ],
    },
    {
      path: '/shopdetails',
      name: 'shopdetails',
      component: shopdetails
    },
    {
      path: '/gowuche',
      name: 'gowuche',
      component: gowuche
    },
  ]
})



