// 我的优惠页面
<template>
  <div id="bc">
    <!-- 头部 -->
    <div id="com_blue">
      <img @click="goBack()" src="../imgs/back.png" alt>
      <span>我的优惠</span>
    </div>
    <transition name="el-fade-in-linear">
      <router-view/>
    </transition>
  </div>
</template>
<script>
export default {
  name: "myyouhui",
  data() {
    return {};
  },
  methods: {
    goBack() {
      this.$router.push({
        name: "fourth"
      });
    }
  },
};
</script>
<style scoped>
#com_blue {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
}
</style>



